import React, { useState, useEffect } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Provider_name from "../../Assets/Images/provider_name.png";
import Favourite from "../../Assets/Images/favourite.png";
import ServiceList9 from "../../Assets/Images/service_list 9.png";
import ServiceList8 from "../../Assets/Images/service_list 8.png";
import ServiceList10 from "../../Assets/Images/service_list 10.png";
import ServiceList14 from "../../Assets/Images/service_list 14.png";
import HttpClient from "../../utils/HttpClient";

import { Link } from "react-router-dom";
// import { ClickAwayListener } from "@material-ui/core";
const RelatedService = (props) => {
  const [data, setdata] = useState([]);
  useEffect(() => {
    getservice();
  }, []);

  const getservice = async () => {
    let data = {
      cat_id: props.catid,
      service_id: props.service_id,
    };
    console.log("data", data);
    let result = await HttpClient.requestData(
      "allRelatedServiceList",
      "POST",
      data
    );
    console.log("allRelatedServiceList", result);
    if (result && result.status) {
      setdata(result.data);
    } else {
    }
  };

  const settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 4,

    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 400,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  const click = (id) => {
    window.location.href = "/service-details/" + id;
  };
  return (
    <div className="related_services mt-3">
      <Slider {...settings}>
        {data.map((item, index) => {
          // let imgg = item.image.length > 0 || item.image != null ? item.image[0] : "";
          return (
            <div className="px-md-4 mb-4" key={index}>
              <div className="card service_list">
                <div>
                  {" "}
                  {item.image != null && item.image.length > 0 ?
                    <img
                      src={"https://admin.blitzcode.online/" + item.image[0]}
                      alt="img"
                      className="relative service_list_img"
                      style={{ width: "100%" }}
                    /> :
                    <img
                      src=""
                      alt="img"
                      className="relative service_list_img"
                      style={{ width: "100%" }}
                    />
                  }
                  {/* <img src={Favourite} alt="img" className="absolute m-2" /> */}
                </div>

                <div className="card-body service_list_text">
                  <div className="d-flex">
                    <i className="fas fa-star my-1 mr-2"></i>
                    <h6>{item.avgRating}/5</h6>
                  </div>
                  {/* <Link to={"/service-details/"+item._id}> */}
                  <p className="card-text" onClick={() => click(item._id)}>
                    {item.name}
                  </p>
                  {/* </Link> */}
                  <h5>$ {item.price}</h5>
                  <div className="d-flex my-4">
                    <img
                      src={
                        "https://admin.blitzcode.online/" +
                        item.shop_details.user_data.image
                      }
                      alt="img"
                      className="mr-2"
                      style={{
                        width: "100%",
                        height: "25px",
                        maxWidth: "25px",
                      }}
                    />
                    <h4>
                      {item.shop_details.user_data.firstName +
                        " " +
                        item.shop_details.user_data.lastName}
                    </h4>
                  </div>
                </div>
              </div>
            </div>
          );
        })}
      </Slider>
    </div>
  );
};
export default RelatedService;
