import React, { useState, useEffect } from "react";
import Footer from "../../Defaults/Footer";
import ServiceList from "../../Views/Services/ServiceList";
import ServiceList1 from "../../Assets/Images/service_list 1.png";
import ServiceList2 from "../../Assets/Images/service_list 2.png";
import ServiceList3 from "../../Assets/Images/service_list 3.png";
import ServiceList4 from "../../Assets/Images/service_list 4.png";
import ServiceList5 from "../../Assets/Images/service_list 5.png";
import ServiceList6 from "../../Assets/Images/service_list 6.png";
import ServiceList7 from "../../Assets/Images/service_list 7.png";
import ServiceList8 from "../../Assets/Images/service_list 8.png";
import ServiceList9 from "../../Assets/Images/service_list 9.png";
import ServiceList10 from "../../Assets/Images/service_list 10.png";
import ServiceList11 from "../../Assets/Images/service_list 11.png";
import ServiceList12 from "../../Assets/Images/service_list 12.png";
import ServiceList13 from "../../Assets/Images/service_list 13.png";
import ServiceList14 from "../../Assets/Images/service_list 14.png";
import { reactLocalStorage } from "reactjs-localstorage";
import HttpClient from "../../utils/HttpClient";
import Loader from "../../Component/loader";
const Index = () => {
  const [service, setService] = useState([]);
  const [shopid, setshopid] = useState("");
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    fetchShopId();
    fetchService();
  }, []);

  const refresh = (val) => {
    if (val) {
      fetchShopId();
    }
  };
  const userdata = reactLocalStorage.getObject("userData");
  const fetchService = async (shopid) => {
    // console.log("idd", id);
    setLoading(true);
    let result = await HttpClient.requestData(
      "shop/all-services/" + shopid,
      "GET"
    );
    console.log("ownservicedata", result);
    if (result && result.status) {
      let data = result.data;
      setService(data?data:[]);
      console.log("service", service);
      setTimeout(() => {
        setLoading(false);
      }, 1);
    } else {
    }
  };

  const fetchShopId = async () => {
    let result = await HttpClient.requestData("seller/" + userdata._id, "GET");
    console.log("seller", result.data);
    if (result && result.status) {
      // let data = result.shop_id;
      // console.log("shopId",data);
      fetchService(result.shop_id);
    } else {
    }
  };
  return (
    <div>
      {loading ? (
        <Loader />
      ) : (
        <div className="container-fluid my-4">
          <div className="row">
            <div className="col-md-12 text-center mb-3">
              <h1>My Services</h1>
            </div>
          </div>
          <ServiceList
            data={service}
            type="edit"
            key={service.toString()}
            refresh={refresh}
          />
        </div>
      )}
      <section>
        <Footer />
      </section>
    </div>
  );
};
export default Index;
