import React, { useState } from "react";
import Footer from "../../Defaults/Footer";

const Grievance = () => {
  const [other, setOther] = useState(false);
  return (
    <div className="Customer-service feedback">
      <div className="container-fluid">
        <div className="row">
          <div className="col-md-12 text-center my-4">
            <h2>Grievance</h2>
          </div>
        </div>
        <div className="row">
          <div className="col-md-8  my-4 m-auto">
            <div className="row">
              <div className="col">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Person Name"
                />
              </div>
              <div className="col">
                <input
                  type="email"
                  className="form-control"
                  placeholder="Email"
                />
              </div>
            </div>
            <div className="row mt-3">
              <div className="col">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Contact Number"
                />
              </div>
              <div className="col">
                <input type="text" className="form-control" placeholder="URL" />
              </div>
            </div>
            <div className="form-group my-3">
              <label htmlFor="exampleFormControlSelect1">Report Against</label>
              <select className="form-control" id="exampleFormControlSelect1">
                <option>problem</option>
                <option>problem</option>
                <option onClick={() => setOther(true)}>others</option>
              </select>
            </div>

            <div className="form-group">
              <label htmlFor="exampleFormControlTextarea1">
                Write Your Report
              </label>
              <textarea
                className="form-control"
                id="exampleFormControlTextarea1"
                rows={3}
              />
            </div>
            <div className="form-group mt-3">
              <input
                type="file"
                className="form-control-file btn"
                id="exampleFormControlFile1"
              />
            </div>
            <div className="text-center my-4">
              <button className="btn submit_btn">Submit</button>
            </div>
          </div>
        </div>
      </div>
      <section>
        <Footer />
      </section>
    </div>
  );
};

export default Grievance;
