import React, { useState,useEffect } from "react";

import { Link } from "react-router-dom";
import { reactLocalStorage } from "reactjs-localstorage";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import HttpClient from "../../../utils/HttpClient";
const ProductUpdated = (props) => {
  console.log("props",props.data);
  const [fillheart, setFillheart] = useState(false);
  const [cartData, setcartData] = useState([]);
  const [added, setadded] = useState(false);
  const [proId, setproId] = useState("");
  const loginstatus = reactLocalStorage.getObject("loginstatus");

  const userData = reactLocalStorage.getObject("userData");

  useEffect(() => {
    getcart();
    isAlreadyAdded();
  },[]);
  const addcart = async (val) => {
    console.log("object", val);
    // window.location.href="/add-cart";
    // isAlreadyAdded(val);
    
    let data = {
      prod_id: val._id,
      user_id: userData._id,
      productname: val.name,
      qty: 1,
      price: val.selling_price,
      image: val.image[0],
    };
    // console.log("data", data);
    let result = await HttpClient.requestData("add-to-cart", "POST", data);
    console.log("add to cart", result);
    if (result && result.status) {
      toast.success(result.message, {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
      // isAlreadyAdded();
      // setproId(result.data.prod_id);
      // if (val._id === result.data.prod_id) {
      //   console.log(val._id);
      //   console.log(result.data.prod_id);
      //   setadded(true);
      //   // setproAdded("Added")
      // }
      // else{
      //   setadded(false);
      // }
    } else {
      toast.error(result.message, {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  };

  const buynow = async (val) => {
    // console.log("object", val);
    // window.location.href="/add-cart";
    let data = {
      prod_id: val._id,
      user_id: userData._id,
      productname: val.name,
      qty: 1,
      price: val.selling_price,
      image: val.image[0],
    };
    // console.log("data", data);
    let result = await HttpClient.requestData("add-to-cart", "POST", data);
    console.log("result", result);
    if (result && result.status) {
      window.location.href = "/checkout";
    } else {
    }
  };

  const wishlist = async (val) => {
    let data = {
      prod_id: val._id,
      user_id: userData._id,
      productname: val.name,
      qty: 1,
      price: val.selling_price,
      image: val.image[0],
    };
    console.log("data", data);
    let result = await HttpClient.requestData("wishlist", "POST", data);
    console.log("result", result);
    if (result && result.status) {
      // props.cartdec(result.status);
      props.refresh(result.status);
      toast.success("Added to Wishlist", {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    } else {
    }
  };

  const alert = () => {
    toast.error("You have to login first", {
      position: "bottom-right",
      autoClose: 1000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    });
  };

  const getcart = async () => {
    let result = await HttpClient.requestData(
      "get-cart/" + userData._id,
      "GET"
    );
    console.log("cart data",result);
    if (result && result.status) {
      setcartData(result.data);
    //   console.log("cartData",cartData);
    //   let proId = cartData.map((item) => {
    //     return item.prod_id;
    //   });
    //  proId.map((item) => {
    //    props.data.map((item2) => {
    //      if (item2._id == item) {
    //        setadded(true);
    //      } else {
    //       //  setadded(false);
    //      }
    //    })
    //  })
      // console.log("proId",proId);
      // let addedId = props.data.filter((item,index) => item._id === proId);
      // console.log("addedId",addedId);
    } else {
      setcartData([]);
    }
    
  };

  const isAlreadyAdded = () => {
    // console.log("item",itemId);
    // let id = props.data.filter((item) => item._id === proId);
    // console.log("props id",id[0]._id);
    // if (itemId === id[0]._id) {
    //   return true;
    // }
    props.data.map((item,index) => {
      cartData.length && cartData.map((item2,index2) => {
          if (item2.prod_id == item._id) {
            console.log("cart id----",item2.prod_id);
            console.log("props id----",item._id);
            setadded(true);
            // console.log("status",added);
            // return true;
          } else {
            setadded(false);
          }
      })
    })
    // return false;
  };

  const deletee = async (item) => {
    const arr = item.filter((item) => item.user_id == userData._id);
    // console.log("arrrr",arr[0]._id);
    let id = arr[0]._id;
    let data = {};
    let result = await HttpClient.requestData(
      "deleteWishlist/" + id,
      "delete",
      data
    );
    console.log("result", result);
    if (result && result.status) {
      props.refresh(result.status);
      toast.success("Removed From Wishlist", {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    } else {
    }
  };
  return (
    <div className="row  px-lg-4">
      <ToastContainer />
      {props.data.length > 0
        ? props.data.map((item, index) => {
            let sum = 0;
            let avg = 0;
            item.review_data.map((itemm, index) => {
              return (sum = Number(sum) + Number(itemm.rating));
            });
            // console.log("sum", sum);
            avg = Number(sum) / Number(item.review_data.length);
            // console.log(item.review_data.length)
            let indexx = 0;
            if (loginstatus) {
              indexx = item.wishlist_data.findIndex(
                (item) => item.user_id == userData._id
              );
            }

            // console.log("index",indexx)
            return index < 8 ? (
              <div className="col-md-3 mb-5" key={index}>
                <div className="card product_box2 ">
                  <Link to={"/product-details/" + item._id}>
                    <div className="text-center">
                      <img
                        src={"https://admin.blitzcode.online/" + item.image[0]}
                        className="card-img-top"
                        alt="img"
                      />
                    </div>
                  </Link>
                  <div className="card-body product_list_text2 pt-2">
                    <Link to={"/product-details/" + item._id}>
                      <div className="d-lg-flex  justify-content-between  mt-3">
                        <h6>{item.name}</h6>
                        <div className="rating my-lg-0 my-3 ">
                          <i className="fas fa-star my-1 mr-2"></i>
                          {item.avgRating ? item.avgRating.toFixed(1) : "0"}/5
                        </div>
                      </div>
                    </Link>
                    <div className="d-flex justify-content-between">
                      <div className="d-flex flex-wrap mb-3">
                        <del> {item.mrp}</del>
                        <p className="card-text px-3 ">
                           {item.selling_price}
                        </p>
                      </div>
                      {loginstatus ? (
                        <div>
                          {indexx > 0 ? (
                            <div
                              className=""
                              onClick={() => {
                                // wishlist(item);
                                setFillheart(true);
                                deletee(item.wishlist_data);
                              }}
                            >
                              <i className="fas fa-heart text-danger"></i>
                            </div>
                          ) : (
                            <div
                              className=""
                              onClick={() => {
                                setFillheart(false);
                                wishlist(item);
                              }}
                            >
                              <i className="far fa-heart text-danger"></i>
                            </div>
                          )}
                          {/* {fillheart ? (
                            <div
                              className=""
                              onClick={() => {
                                setFillheart(false);
                              }}
                            >
                              <i className="fas fa-heart text-danger"></i>
                            </div>
                          ) : (
                            <div
                              className=""
                              onClick={() => {
                                wishlist(item);
                                setFillheart(true);
                              }}
                            >
                              <i className="far fa-heart text-danger"></i>
                            </div>
                          )} */}
                        </div>
                      ) : (
                        <div className="" onClick={alert}>
                          {/* <i className="far fa-heart text-danger"></i> */}
                        </div>
                      )}
                    </div>

                    <div className="d-md-flex  justify-content-between align-items-center">
                      {/* <Link to="/checkout"> */}{" "}
                      {loginstatus ? (
                        <Link
                          className="Product_buy_btn btn"
                          to="/checkout"
                          onClick={() => buynow(item)}
                        >
                          Buy Now
                        </Link>
                      ) : (
                        <button className="Product_buy_btn btn" onClick={alert}>
                          Buy Now
                        </button>
                      )}
                      {/* </Link> */}
                      {loginstatus ? (
                         <button
                         className=" btn"
                         onClick={() => addcart(item)}
                       >
                         {/* {added ? "Added" : " + Add to Cart"} */}
                         + Add to Cart
                       </button>
                        // <>
                        //   {added ? (
                        //     <button
                        //     className=" btn"
                        //     disabled
                        //   >
                        //     Added
                        //   </button>
                        //   ) : (
                        //     <button
                        //     className=" btn"
                        //     onClick={() => addcart(item)}
                        //   >
                        //     + Add to Cart
                        //   </button>
                        //   )}
                        // </>
                      ) : (
                        <button className=" btn" onClick={alert}>
                          + Add to Cart
                        </button>
                      )}
                    </div>
                  </div>
                </div>
              </div>
            ) : null;
          })
        : null}
    </div>
  );
};
export default ProductUpdated;
