import React from "react";
import { Link } from "react-router-dom";
import RatingChanged from "../../Component/RatingChanged";
import { reactLocalStorage } from "reactjs-localstorage";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import HttpClient from "../../utils/HttpClient";
const ListItem = (props) => {
  const userData = reactLocalStorage.getObject("userData");
  const addcart = async (val) => {
    let data = {
      prod_id: val.prod_id,
      user_id: userData._id,
      productname: val.productname,
      qty: 1,
      price: val.price,
      image: val.image,
    };
    console.log("data", data);

    let result = await HttpClient.requestData("add-to-cart", "POST", data);
    console.log("result", result);
    if (result && result.status) {
      toast.success(result.message, {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
      props.success(result.status);
    } else {
      toast.error(result.message, {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  };

  const deletee = async (val) => {
    let data={};
    let result = await HttpClient.requestData(
      "deleteWishlist/" + val._id,
      "delete",
      data
    );
    console.log("result", result);
    if (result && result.status) {
      props.success(result.status);
    } else {
    }
  };
  return (
    <div className="bottomSection mt-4" key={props.key}>
      <ToastContainer />
      <div className="card wish_card">
        <div className="card-body d-lg-flex">
          <div className="web_img">
            <img
              src={"https://admin.blitzcode.online/" + props.data.image}
              alt="img"
              className="img-fluid"
            />
          </div>

          <div className="web_box ml-lg-4 my-lg-0 my-3">
            <div className="row  ">
              <div className="col-md-8">
                <h4>{props.data.productname}</h4>
                <p>
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit. Etiam
                  elementum ac diam at convallis. Nulla diam libero, maximus sed
                  justo et, convallis tempus dolor. Etiam non magna vel felis.
                </p>
              </div>
              <div className="col-md-4 ">
                {/* <p className=" text-right">Item Added 28 Oct </p> */}
                <div className="d-flex justify-content-end ">
                  <button
                    className="btn btn-danger"
                    type="button"
                    onClick={() => addcart(props.data)}
                  >
                    Add to Cart
                  </button>

                  <button
                    className="btn btn-secondary register_btn ml-3"
                    type="button"
                    onClick={() => deletee(props.data)}
                  >
                    Delete
                  </button>
                </div>
              </div>
            </div>
            <div>
              {/* <RatingChanged /> */}
            </div>
            <div className="d-flex">
              <h5>{props.data.price}</h5>
              <p className="mx-md-3 mt-1 text-success">
                <i className="fas fa-check-circle mr-2" />
                In Stock
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default ListItem;
