import React, { useEffect, useState, useRef } from "react";
import Chat2 from "../../../Assets/Images/chat_img2.png";
import MessageBody from "./messageBody";
import db from "../../../Firebase/fbconfig";
import InfiniteScroll from "react-infinite-scroll-component";
import Loader from "../../../Component/loader";
import {
  ref,
  get,
  child,
  limitToLast,
  onChildAdded,
  query,
  orderByKey,
  endAt,
  onValue,
  update,
  off
} from "firebase/database";
import { reactLocalStorage } from "reactjs-localstorage";
import moment from "moment";
import ImageMsg from "./ImageMsg";
const data = [];
const MessageList = (props) => {
  // console.log("messagelistcomponent");
  const messageEl = useRef(null);
  const [msgarr, setmsgarr] = useState([]);
  const [isloading, setisloading] = useState(true);
  const [hasMore, sethasMore] = useState(true);
  const [chatRoomId, setChatRoomId] = useState(null);
  const userData = reactLocalStorage.getObject("userData");
  const chatRef = useRef(null);
  // console.log("props.sender", props.sender);
  useEffect(() => {
    // if(chatRef.current != null){
    // console.log("called")
    off(ref(db, `/Chat/${chatRoomId}/messages/`), undefined, chatRef.current);
    // }

    getchat();
    setChatRoomId(props.roomId)
  }, [props.roomId]);

  useEffect(() => {
    if (messageEl) {
      messageEl.current.addEventListener("DOMNodeInserted", (event) => {
        const { currentTarget: target } = event;
        target.scroll({ top: target.scrollHeight, behavior: "smooth" });
      });
    }
  }, []);

  const getchat = async () => {
    const dbRef = ref(db);
    // console.log("/Chat/" + props.roomId + "/messages/");
    // child(dbRef, "/Chat/" + props.roomId + "/messages/", limitToLast(10))
    get(query(ref(db, `/Chat/${props.roomId}/messages/`), limitToLast(10)))
      .then((snapshot) => {
        if (snapshot.exists()) {
          // console.log(snapshot.val());
          setisloading(false);
          sethasMore(true);
          setmsgarr(Object.values(snapshot.val()));
          getFirebaseListener(Object.values(snapshot.val()));
        } else {
          sethasMore(false);
          setmsgarr([]);
          getFirebaseListener([]);
          console.log("No data available");
        }
      })
      .catch((error) => {
        console.error(error);
      });
  };

  const getFirebaseListener = (oldVal) => {
    // console.log("/Chat/" + props.roomId + "/messages/");

    const dbRef = ref(db, "/Chat/" + props.roomId + "/messages/");

    const dbquery = query(dbRef, limitToLast(1));

    chatRef.current = onChildAdded(dbquery, (snapshot) => {
      // console.log("snapshot.val()",snapshot.val());
      if (snapshot.exists()) {
        // console.log("fbData", snapshot.val());
        // console.log("oldVal", oldVal);
        let fbData = snapshot.val();
        // if(fbData.sender_id == props.sender.userId){
        let msgIndex = oldVal.findIndex((it) => it.msgId == fbData.msgId);
        if (msgIndex == -1) {
          setmsgarr((msgarr) => [...msgarr, fbData]);
          const updates = {};
          updates[
            `/chatList/${userData._id}/${props.sender.userId}/unseen`
          ] = false;
          update(ref(db), updates);
        }
        // }

      } else {
        console.log("No data available");
      }
    });
  };

  const sorted = () => {
    let a;
    a = msgarr.sort(function (a, b) {
      return b.send_time < a.send_time ? -1 : b.send_time > a.send_time ? 1 : 0;
    });
    console.log(a);
    return a;
  };

  const fetchMoreData = () => {
    // alert("hgfy")
    if (hasMore) {
      // setisloading(true);
      let arrLength = sorted().length;
      let lastKeyRef = sorted()[arrLength - 1].nodeId;
      // console.log("lastKeyRef", lastKeyRef);
      const dbRef = ref(db, "/Chat/" + props.roomId + "/messages/");

      const dbquery = query(
        dbRef,
        orderByKey(),
        limitToLast(11),
        endAt(lastKeyRef)
      );
      onValue(dbquery, (snapshot) => {
        // console.log("snapshot.val()",snapshot.val());
        if (snapshot.exists()) {
          let fbData = Object.values(snapshot.val());
          let finalArr = fbData.filter((it) => it.nodeId != lastKeyRef);
          setmsgarr((msgarr) => [...msgarr, ...finalArr]);
          // setLoadMore(false);
          // setisloading(false);
          if (finalArr.length == 0) {
            sethasMore(false);
          }
        } else {
          // setLoadMore(false);
          // setisloading(false);
          sethasMore(false);
          console.log("No data available");
        }
      });
    }
  };

  return (
    <div className="message_list px-3" ref={messageEl}>
      <div className="row my-3">
        <div className="col-md-12 text-center">Today</div>
      </div>
      <InfiniteScroll
        loadMore={fetchMoreData}
        hasMore={hasMore}
        loader={"Loading..."}
        threshold={150}
        useWindow={false}
        dataLength="10"
        inverse={true}
      >
        {msgarr.length > 0
          ? msgarr.map((item, index) => {
            return (
              <>
              {/* {console.log("itemcall", item)} */}
                {
                  item?.type == "call" ?
                    <li className="log_time">
                      {moment(item.callTime).format('hh:mm A')}<br />
                      <span>
                        {
                          item.callStatus == "Recieved" ?
                            `Call ${item.callDuration}`
                            :
                            item.sender_id == userData._id ?
                              'Not Connected'
                              :
                              'Missed Call'
                        }
                      </span><br />
                      <span>{moment(item.callTime).format('DD MMM, YYYY')}</span>
                    </li>
                    :

                    <div
                      className={
                        item.sender_id == userData._id
                          ? "userSended" : "userRecieved"
                      }
                      key={index}
                    >
                      <div className="chat_body my-3" key={item.msgId}>
                        <div
                          className={
                            item.offer ? "SendText w-100" : "d-flex SendText w-100"
                          }
                        >
                          {/* <div className="mb-auto">
                  <img src={item.img} alt="img" className="avatar_50" />
                </div> */}
                          <div className="mx-3 my-auto   ">
                            <div className="message_text">
                              <span>{moment(item.send_time).format("LT")}</span>

                              {/* {item.image ? (
                          <ImageMsg url={item.message} />
                        ) : (
                          <p className="mb-0">{item.message}</p>
                        )} */}
                              {item.image ? (
                                <ImageMsg url={item.message} />
                              ) : item.offer ? (
                                <MessageBody
                                  offername={item.offername}
                                  price={item.price}
                                  details={item.details}
                                  image={item.imagepath}
                                  serviceid={item.service_id}
                                />
                              ) : (
                                <p className="mb-0">{item.message}</p>
                              )}
                              {/* <MessageBody/> */}
                              {/* <ImageMsg /> */}
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                }
              </>
            );
          })
          : null}
      </InfiniteScroll>
    </div>
  );
};
export default MessageList;
