import React, {useState,useEffect} from "react";
import HttpClient from "../../utils/HttpClient";
import {  toast } from "react-toastify";
import validator from "validator";
import Warning from "../../Component/Warning";
import Success from "../../Component/Success";

const Contact = () => {
  const [name,setName] = useState("");
  const [email, setEmail] = useState("");
  const [category, setCategory] = useState([]);
  const [issue, setIssue] = useState("");
  const [cat, setcat] = useState([]);

  const [emailerror, setemailerror] = useState(false);

  const [emailvalid, setemailvalid] = useState(false);
  const [warning, setwarning] = useState(false);
  const [successs, setsuccesss] = useState(false);
  useEffect(() => {
    fetchCat();
  }, []);

  const fetchCat = async () => {
    let result = await HttpClient.requestData("service", "GET");
    // console.log("Cat", result);
    if (result && result.status) {
      let data = result.data;
      // console.log("data", data);
      setcat(data);
      // console.log("setcat", cat);
    } else {
    }
  };

  
  const handleSubmit = async () => {
    let patternn =
      /^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{7,}$/;
      if (email == "") {
        setemailerror(true);
      } else if (!validator.isEmail(email)) {
        setemailvalid(true);
      }
        let sendData = {
          name: name,
          email: email,
          cat_id: category,
          message: issue
        };
  
        let result = await HttpClient.requestData("sellercontact", "POST", sendData);
        console.log("sellercontact", result);
        if (result && result.status) {
          toast.success("Submit successfully", {
            position: "bottom-right",
            autoClose: 3000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
          setName("");
          setEmail("");
          setCategory("");
          setIssue("");

        } else {
          toast.error("Already Submitted", {
            position: "bottom-right",
            autoClose: 1000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
      } 
    };

  const handlecategory = (event) => {
    const selectedcategory = event.target.value;
    setCategory(selectedcategory);
  };
   
  return (
   
    <div className="withdraw">
      <div className="row mt-4">
        <div className="col-md-12 text-center">
          <h1>Contact Us</h1>
        </div>
        <div className="col-md-6 offset-md-3 my-3">
          <div className="card py-3">
            <div className="card-body">
              <div className="form-group">
                <input
                  type="text"
                  className="form-control"
                  id="exampleInput"
                  aria-describedby="emailHelp"
                  placeholder="Your Name"
                  onChange={(val) => {
                    if (val.target.value.match("^[a-zA-Z ]*$") != null) {
                      setName(val.target.value);
                    }
                  }}
                  value={name}
                />
              </div>
              <div className="form-group my-3">
                <input
                  type="email"
                  className="form-control"
                  id="exampleInput"
                  aria-describedby="emailHelp"
                  placeholder="Email ID"
                  value={email}
                    onChange={(val) => {
                      setEmail(val.target.value);
                      if (val.target.value == "") {
                        setemailerror(true);
                      } else {
                        setemailerror(false);

                        if (validator.isEmail(email)) {
                          setemailvalid(false);
                        } else {
                          setemailvalid(true);
                        }
                      }
                    }}
                  />
                </div>
                {warning ? (
                  <Warning warning="The email entered already exists" />
                ) :  emailerror ? (
                  <Warning warning="Please Enter Email" />
                ) : emailvalid ? (
                  <Warning warning="Please Enter Valid Email" />
                )  : null}

                {successs ? <Success success="Sign up successful" /> : null}
              <div className="form-group">
                <select 
                className="form-control" 
                id="exampleFormControlSelect1"
                onChange={handlecategory}
                value={category}>
                  <option value="">Select Catagory</option>
                  {cat.map((item, index) => {
                    return (
                      <option value={item._id} key={index}>
                        {item.name}
                      </option>
                    );
                  })}
                </select>
              </div>
              <div className="form-group py-3">
                <textarea
                  className="form-control"
                  id="exampleFormControlTextarea1"
                  rows={3}
                  defaultValue={"Enter Your Issue"}
                  onChange={(val) => {
                    if (val.target.value.match("^[a-zA-Z ]*$") != null) {
                      setIssue(val.target.value);
                    }
                  }}
                  value={issue}
                />
              </div>
              <div className="text-center">
                <button className="btn process_btn"
                 onClick={() => {
                      handleSubmit();
                    }}
                    >
                    Submit</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  
  );
};
export default Contact;
