import React, { useState, useEffect } from "react";
import List from "./List";
import Footer from "../../Defaults/Footer";
import serviceImg from "../../Assets/Images/service_list 3.png";
import HttpClient from "../../utils/HttpClient";
import { reactLocalStorage } from "reactjs-localstorage";
import EmptyWishlist from "../EmptyWishlist/EmptyWishlist";
import Loader from "../../Component/loader";

const userData = reactLocalStorage.getObject("userData");
const Index = () => {
  const [data, setdata] = useState([]);
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    getwishlist();
  }, []);

  const getwishlist = async () => {
    setLoading(true);
    let data = {};

    console.log("data", data);
    let result = await HttpClient.requestData(
      "servicegetWishlist/" + userData._id,
      "GET",
      data
    );
    console.log("result", result);
    if (result && result.status) {
      setdata(result.data);
      setTimeout(() => {
        setLoading(false);
      }, 1);
    } else {
    }
  };

  const success = (val) => {
    if (val) {
      getwishlist();
    }
  };
  return (
    <div>
      <section className="container mt-3">
        <div className="row">
          <div className="col-md-12 text-center">
            <h1>Your Service Wishlists</h1>
          </div>
        </div>
      </section>

      {loading ? (
        <Loader />
      ) : (
        <>
          {" "}
          <section>
            <div className="container-fluid">
              <div className="row">
                {data.length > 0 ? (
                  data.map((item, index) => {
                    return (
                      <div className="col-md-10 mx-auto" key={index}>
                        <List data={item} success={success} />
                      </div>
                    );
                  })
                ) : (
                  <EmptyWishlist type="service" />
                )}
              </div>
            </div>
          </section>
          <section>
            <Footer />
          </section>
        </>
      )}
    </div>
  );
};
export default Index;
