import React, { useEffect, useState } from "react";
import Faq from "../Home/Faq/Faq";
import Footer from "../../Defaults/Footer";
import HttpClient from "../../utils/HttpClient";
import validator from "validator";
import Warning from "../../Component/Warning";
import Success from "../../Component/Success";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const CustomerService = () => {
  const [phone, setphone] = useState("");
  const [email, setemail] = useState("");
  const [question, setquestion] = useState("");
  const [phoneError, setphoneError] = useState(false);
  const [emailError, setemailError] = useState(false);
  const [emailvalid, setemailvalid] = useState(false);
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  const userTyping = async (e) => {
    if (e.keyCode === 13) {
      submitQuestion();
    }
  };

  const submitQuestion = async () => {
    if (phone == "" || email == "" || question == "") {
      toast.error("Please enter all field", {
        position: "bottom-right",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    } else if (emailError == true) {
      toast.error("Please enter valid email", {
        position: "bottom-right",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    } else {
      let sendData = {
        email: email,
        heading: phone,
        qstn: question,
      }
      let result = await HttpClient.requestData("createCservice", "POST", sendData);
      // console.log("submit question",result);
      if (result && result.status) {
        toast.success("Question Added Successfully", {
          position: "bottom-right",
          autoClose: 1000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
        setquestion("");
        setemail("");
        setphone("");
      } else {
        toast.error("Error in Adding Question", {
          position: "bottom-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      }
     
    }
  };
  return (
    <div className="Customer-service feedback">
      <ToastContainer />
      <div className="container-fluid">
        <div className="row">
          <div className="col-md-12 text-center my-4">
            <h2>Customer Service</h2>
          </div>
        </div>
        <div className="row">
          <div className="col-md-8  my-4 m-auto">
            <div className="row">
              <div className="col">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Phone Number"
                  value={phone}
                  onChange={(val) => {
                    if (val.target.value.match("^[0-9]*$") != null) {
                      setphone(val.target.value);
                      if (val.target.value.length > 10) {
                        setphoneError(true);
                      } else {
                        setphoneError(false);
                      }
                    }
                  }}
                />
              </div>
              <div className="col">
                <input
                  type="email"
                  className="form-control"
                  placeholder="Email"
                  value={email}
                  onChange={(val) => {
                    setemail(val.target.value);
                    if (val.target.value == "") {
                      setemailError(true);
                    } else {
                      setemailError(false);

                      if (validator.isEmail(email)) {
                        setemailvalid(false);
                      } else {
                        setemailvalid(true);
                      }
                    }
                  }}
                  onKeyUp={(e) => userTyping(e)}
                />
              </div>
            </div>
            <div className="form-group mt-3">
              <label htmlFor="exampleFormControlTextarea1">Any Question</label>
              <textarea
                className="form-control"
                id="exampleFormControlTextarea1"
                rows={5}
                placeholder="Write Here..."
                value={question}
                onChange={(val) => {
                  if (val.target.value.match("^[a-zA-Z0-9 ]*$") != null) {
                    setquestion(val.target.value);
                  }
                }}
              />
            </div>
            {emailError ? (
              <Warning warning="Please Enter Email" />
            ) : emailvalid ? (
              <Warning warning="Please Enter Valid Email" />
            ) : phoneError ? (
              <Warning warning="Please Enter Phone Less than 10 Digit" />
            ) : null}
            <div className="text-center my-4">
              <button className="btn submit_btn" onClick={submitQuestion}>
                Submit
              </button>
            </div>
          </div>
        </div>
        <div className="row mt-4">
          <div className="col-md-6 m-auto ">
            <div className="row">
              <div className="col-md-6 ">
                <div className="card">
                  <div className="card-body">
                    <h5 className="card-title">Call Back to this Number</h5>
                    <h6 className="card-subtitle mb-2 text-muted">000000000</h6>
                  </div>
                </div>
              </div>
              <div className="col-md-6 ">
                <div className="card">
                  <div className="card-body">
                    <h5 className="card-title">Toll-Free Number</h5>
                    <h6 className="card-subtitle mb-2 text-muted">0000000</h6>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        {/* <div className="row my-5">
          <div className="col-md-12 text-center">
            <h2>FAQ</h2>
          </div>
          <div className="col-md-8 answer my-5 m-auto">
            <Faq />
            <div className="text-center my-5">
              <button href="#" className="btn mb-4  ">
                Load More
              </button>
            </div>
          </div>
        </div> */}
      </div>
      <section>
        <Footer />
      </section>
    </div>
  );
};

export default CustomerService;
