import React, { useEffect, useState } from "react";
import Filter from "./Filter";
import ServiceList from "./ServiceList";
import ServiceList1 from "../../Assets/Images/service_list 1.png";
import ServiceList2 from "../../Assets/Images/service_list 2.png";
import ServiceList3 from "../../Assets/Images/service_list 3.png";
import ServiceList4 from "../../Assets/Images/service_list 4.png";
import ServiceList5 from "../../Assets/Images/service_list 5.png";
import ServiceList6 from "../../Assets/Images/service_list 6.png";
import ServiceList7 from "../../Assets/Images/service_list 7.png";
import ServiceList8 from "../../Assets/Images/service_list 8.png";
import ServiceList9 from "../../Assets/Images/service_list 9.png";
import ServiceList10 from "../../Assets/Images/service_list 10.png";
import ServiceList11 from "../../Assets/Images/service_list 11.png";
import ServiceList12 from "../../Assets/Images/service_list 12.png";
import ServiceList13 from "../../Assets/Images/service_list 13.png";
import ServiceList14 from "../../Assets/Images/service_list 14.png";
import ServicePagination from "../Components/ServicePagination";
import Spell from "../Components/Spell";
import OtherService from "../Components/OtherService";
import Footer from "../../Defaults/Footer";
import { ReactTitle } from "react-meta-tags";
import Loader from "../../Component/loader";
import HttpClient from "../../utils/HttpClient";
import { reactLocalStorage } from "reactjs-localstorage";
const Index = (props) => {
  const [service, setService] = useState([]);
  const id = props.match.params.id;
  const [loading, setLoading] = useState(false);
  const [catname, setcatname] = useState("");
  const userData = reactLocalStorage.getObject("userData");
  useEffect(() => {
    // fetchShopId();
    fetchService();
    window.scrollTo(0, 0);
  }, []);
  // const items = [];
  const [productpaginator, setproductpaginator] = useState({});
  const fetchService = async (next) => {
    console.log("idd", id);
    setLoading(true);
    let sendData = next ? next : "1";
    let result = await HttpClient.requestData(
      "service/shop-services/" + id + "/" + sendData,
      "GET"
    );
    console.log("servicefetchbycategory", result);
    if (result && result.status) {
      let data = [];

      if (result.data.itemsList.length > 0) {
        data = result.data.itemsList.filter(
          (item) => item.shop_details[0].userid != userData._id
        );
      } else {
        data = [];
      }

      // console.log("data", data);
      setproductpaginator(result.data.paginator);
      setcatname(result.category_data.name);
      setService(data);
      setTimeout(() => {
        setLoading(false);
      }, 1);
      // console.log("Servicedata", service);
    } else {
    }
  };

  const filer = (val, data) => {
    if (val) {
      setService(data);
    } else {
      setService(data);
    }
  };

  const nextpage = (val) => {
    fetchService(val);
  };
  return (
    <div>
      <div className="wrapper">
        <ReactTitle title="Astrophy-Services" />
      </div>
      {loading ? (
        <Loader />
      ) : (
        <>
          {" "}
          <section className="Spell">
            <Spell />
          </section>
          <section className="Services">
            <div className="container" style={{ maxWidth: "1200px" }}>
              <h3 className=" my-4 px-3">Result for {catname}</h3>
              <Filter page="services" filer={filer} />
            </div>
          </section>
          <section>
            <div className="container" style={{ maxWidth: "1200px" }}>
              <ServiceList data={service} />
            </div>
          </section>
          <section className="container">
            <div className="row">
              <div className="col-md-12 ">
                <ServicePagination
                  productpaginator={productpaginator}
                  nextpage={nextpage}
                />
              </div>
            </div>
          </section>
          <section>
            <div className="container" style={{ maxWidth: "1200px" }}>
              <div className="row">
                <div className="col-md-12 my-5">
                  <div className="service_head">
                    <h6>Reading</h6>
                    <h3 className="my-4">Other Services</h3>
                  </div>
                </div>
              </div>
              <OtherService />
            </div>
          </section>
          <section>
            <Footer />
          </section>
        </>
      )}
    </div>
  );
};
export default Index;
