import React, { useEffect, useState } from "react";
import { ReactTitle } from "react-meta-tags";
import { Link } from "react-router-dom";
import cardImg1 from "../../Assets/Images/detailspro_pic.png";
import cardImg2 from "../../Assets/Images/il_794xN 1 (1).png";
import { reactLocalStorage } from "reactjs-localstorage";
import HttpClient from "../../utils/HttpClient";
import Footer from "../../Defaults/Footer";
import CartList from "./CartList";
import Loader from "../../Component/loader";
import moment from "moment";
const Cart = () => {
  useEffect(() => {
    window.scrollTo(0, 0);
    getcart();
  }, []);
  const [loading, setLoading] = useState(false);
  const [coupon, setcoupon] = useState("");
  const [discount, setdiscount] = useState(0);
  const userData = reactLocalStorage.getObject("userData");
  const [sum, setsum] = useState(0);
  const [loop, setloop] = useState([]);
  const [count, setcount] = useState(0);

  const [valid, setvalid] = useState(false);

  const [invalid, setinvalid] = useState(false);
  const [dateInvalid, setdateInvalid] = useState(false);

  const [couponApplied, setcouponApplied] = useState(false)

  const [coupondata, setcoupondata] = useState([]);


  const setarr = (index, value) => {
    let arr = loop;
    arr[index].notes = value;
    setloop(arr);
    // console.log("loop", arr);
  };
  const getcart = async () => {
    // setLoading(true);
    let data = {};
    // console.log("data", data);
    let result = await HttpClient.requestData(
      "get-cart/" + userData._id,
      "GET",
      data
    );
    console.log("result", result);
    if (result && result.status) {
      setloop(result.data);
      setcount(result.data.length);
      let sum = 0;
      result.data.forEach((element) => {
        sum = sum + element.price * element.qty;
      });
      // console.log("sum", sum);
      setsum(sum);
      setTimeout(() => {
        setLoading(false);
      }, 1);
    } else {
    }
  };

  const success = (val) => {
    console.log("object", val);
    if (val) {
      getcart();
    }
  };

  const cartdec = (val) => {
    console.log("object", val);
    if (val) {
      getcart();
    }
  };

  const apply = async () => {
    if (coupon != "") {
      setcouponApplied(false);
      let data = {
        name: coupon,
      };
      console.log("coupon", data);

      let result = await HttpClient.requestData("checkCoupon", "POST", data);
      console.log("checkCoupon", result);
      if (result && result.status) {
        setcoupondata(result.data);
        // var d1 = new Date("2020-09-10");
        // var d2 = new Date(result.data.expdate);

        // var same = d1.getTime()< d2.getTime();

        // console.log("same", same);
        if (result.data.minprice < sum) {
          // alert("Coupon applied successfully");
          setvalid(true);
          setinvalid(false);
          let percent = 0;
          if (result.data.discount_type == "Flat discount") {
            percent = result.data.discount_value;
          } else {
            percent = sum * (result.data.discount_value / 100);
          }

          console.log("percent", percent);

          setdiscount(Number(percent));
        } else {
          setinvalid(true);
          setvalid(false);
        }
        if (result.data.expdate) {
          setvalid(true);
          setdateInvalid(false);
        } else {
          setdateInvalid(true);
          setvalid(false);
        }
      } else {
        // alert("coupon not valid");
        setinvalid(true);
        setvalid(false);
      }
    } else {
      setcouponApplied(true);
    }

  };

  const remove = () => {
    setdiscount(0);
    setcoupon("");
    setvalid(false);
  }

  const checkout = async () => {
    reactLocalStorage.setObject("coupondetails", coupondata);
    reactLocalStorage.setObject("discount", { discount: discount });
    window.location.href = "/checkout";
  };

  return (
    <div>
      <div className="wrapper">
        <ReactTitle title="Astrophy - Add to Cart" />
      </div>
      {loading ? (
        <Loader />
      ) : (
        <>
          {" "}
          <section>
            <div className="container-fluid">
              <div className="row px-4 pt-4">
                <div className="col-md-12 cart_head">
                  <h3>{count} Items in your Basket</h3>
                </div>
              </div>
              <div className="row px-4">
                <CartList
                  data={loop}
                  success={success}
                  cartdec={cartdec}
                  setarr={setarr}
                />
                <div className="col-md-4 ">
                  <div className="cart_box_right p-4 my-3">
                    <div className="border-bottom my-4">
                      <h4>Your Cart</h4>
                      <div className="d-flex justify-content-between">
                        <p>Item(s) Total</p>
                        <p> {sum}</p>
                      </div>
                      <div className="d-flex justify-content-between">
                        <p>Discount</p>
                        <p>-  {discount.toFixed(2)}</p>
                      </div>
                    </div>
                    <div className="border-bottom my-4">
                      <div className="d-flex justify-content-between">
                        <h5>Sub Total</h5>
                        <h5> {sum - discount}</h5>
                      </div>
                      {/* <div className="d-flex justify-content-between">
                  <p>Delivery Fees</p>
                  <p> 20.00</p>
                </div> */}
                    </div>
                    <div className="border-bottom my-4">
                      <h6>Apply Coupon Code</h6>
                      <div className="form-group">
                        <div className="d-flex border_here">
                          <input
                            type="text"
                            className="form-control border-0 bg-white px-2 my-auto w-100"
                            id="exampleFormControlInput1"
                            placeholder="Enter Coupon Code"
                            value={coupon}
                            onChange={(val) => setcoupon(val.target.value)}
                          />

                          {count == 0 ? (
                            <button
                              className="my-auto mr-0 btn apply_coup_btn "
                              onClick={apply}
                              disabled
                            >
                              Apply
                            </button>
                          ) : (
                            <button
                              className="my-auto mr-0 btn apply_coup_btn "
                              onClick={apply}
                            >
                              Apply
                            </button>
                          )}
                          {count == 0 ? null : (
                            <button
                              className="my-auto mr-0 btn apply_coup_btn "
                              onClick={remove}
                            >
                              Remove
                            </button>
                          )}
                        </div>
                      </div>
                      {valid ? (
                        <div className="py-3">
                          <span className="text-success ">
                            <i className="far fa-check-circle"></i> Coupon
                            Applied Successfully
                          </span>
                        </div>
                      ) : null}
                      {invalid ? (
                        <div className="py-3">
                          <span className="text-danger">
                            <i className="far fa-times-circle"></i> Coupon Code
                            Invalid
                          </span>
                        </div>
                      ) : null}
                      {couponApplied ? (
                        <div className="py-3">
                          <span className="text-danger">
                            <i className="far fa-times-circle"></i> Please Enter Coupon Code
                          </span>
                        </div>
                      ) : null}
                      {dateInvalid ? (
                        <div className="py-3">
                          <span className="text-danger">
                            <i className="far fa-times-circle"></i>Coupon code expired
                          </span>
                        </div>
                      ) : null}
                    </div>
                    <div>
                      <div className="d-flex justify-content-between">
                        <h5>Total Amount</h5>
                        <h5> {sum - discount}</h5>
                      </div>{" "}
                      <div className="text-center my-4">
                        {/* <Link to="/checkout"> */}
                        {count == 0 ? (
                          <button
                            className="btn proceed_btn"
                            onClick={checkout}
                            disabled
                          >
                            Proceed to Checkout
                          </button>
                        ) : (
                          <button
                            className="btn proceed_btn"
                            onClick={checkout}
                          >
                            Proceed to Checkout
                          </button>
                        )}
                        {/* </Link> */}
                      </div>
                      <p className="text-center">
                        Local taxes included (where applicable) * Additional
                        duties and taxes{" "}
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </>
      )}

      <section>
        <Footer />
      </section>
    </div>
  );
};
export default Cart;
