import React, { useState } from "react";
// import Table from "./Table";
import { MDBDataTableV5 } from "mdbreact";
import { reactLocalStorage } from "reactjs-localstorage";
import HttpClient from "../../utils/HttpClient";
import Details from "./Details";
const OrderReport = () => {
  const [table, setTable] = useState(false);
  const [fromdate, setfromdate] = useState("");
  const [todate, settodate] = useState("");
  const [disbale, setdisbale] = useState(false);
  const userData = reactLocalStorage.getObject("userData");
  const [dataa, setdata] = useState([]);
  const [activeid, setactiveid] = useState("");
  const [ismodalopen, setismodalopen] = useState(false);

  const onCloseCallBack = (val) => {
    console.log("object", val);
    setismodalopen(val);
  };
  const submit = async () => {
    let data = {
      datefrom: fromdate,
      dateto: todate,
      seller_id: userData._id,
    };
    let result = await HttpClient.requestData(
      "sellerbookhistoryrepo/",
      "POST",
      data
    );
    console.log("result", result);
    if (result && result.status) {
      let data = [];
      let i = 1;
      result.data.forEach((element, index) => {
        let rows = {
          sl: i,

          username: element.firstname + " " + element.lastname,
          order_id: element.order_id,
          total: element.total,
          servicename:
            element.servicecart_data.length > 0
              ? element.servicecart_data[0].servicename
              : null,

          status: (
            <button
              title="Delete"
              className={
                element.status ? "btn btn-success mr-2" : "btn btn-danger mr-2"
              }
            >
              {element.status ? "Confirmed" : "Pending"}
            </button>
          ),
          view: (
            <button
              title="Delete"
              className="btn btn-success mr-2"
              onClick={() => {
                setactiveid(element);
                setTimeout(() => {
                  setismodalopen(true);
                }, 200);
              }}
            >
              View Details
            </button>
          ),
        };
        i++;
        data.push(rows);
      });

      setdata(data);
      setTable(true);
    } else {
      setdata([]);
    }
  };

  const data = {
    columns: [
      {
        label: "Sl.",
        field: "sl",
        width: 150,
      },
      {
        label: "Order Id",
        field: "order_id",
        width: 270,
      },
      {
        label: "Service Name",
        field: "servicename",
        width: 270,
      },
      {
        label: "Total",
        field: "total",
        width: 270,
      },
      {
        label: "Status",
        field: "status",
        width: 100,
      },
      {
        label: "View Details",
        field: "view",
        width: 100,
      },
    ],
    rows: dataa,
  };
  return (
    <div className="order-report">
      <div className="row my-4">
        <div className="col-md-12 text-center">
          <h1>Order Report </h1>
        </div>
      </div>
      <div className="row">
        <div className="col-md-6 mx-auto">
          <form>
            <div className="row">
              <div className="col-12">
                <label htmlFor="inputFromDate">From Date</label>
                <input
                  type="date"
                  className="form-control"
                  placeholder="From Date"
                  value={fromdate}
                  onChange={(val) => {
                    setfromdate(val.target.value);
                  }}
                />
              </div>
              <div className="col-12 my-4">
                <label htmlFor="inputToDate">To Date</label>
                <input
                  type="date"
                  className="form-control"
                  placeholder="To Date"
                  value={todate}
                  onChange={(val) => {
                    settodate(val.target.value);
                    setdisbale(true);
                  }}
                />
              </div>
            </div>
          </form>
          <div className="text-center">
            <button
              type="button"
              className="btn report-btn"
              onClick={submit}
              disabled={!disbale}
            >
              Submit
            </button>
          </div>
        </div>
      </div>
      {table ? (
        <div className="row mt-5">
          <div className="col-md-10 mx-auto">
            {ismodalopen ? (
              <Details
                status={true}
                onCloseCallBack={onCloseCallBack}
                data={activeid}
              />
            ) : null}
            <MDBDataTableV5
              hover
              entriesOptions={[5, 20, 25]}
              entries={5}
              pagesAmount={4}
              data={data}
            />
          </div>
        </div>
      ) : null}
    </div>
  );
};
export default OrderReport;
