import React from "react";
import { reactLocalStorage } from "reactjs-localstorage";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import HttpClient from "../../../utils/HttpClient";
const userData = reactLocalStorage.getObject("userData");
const MessageBody = (props) => {
  const accept = async() => {
    let data = {
      user_id: userData._id,
      serv_id: props.serviceid,
      servicename: props.offername,
      price: props.price,
      image: props.image,
    };
    console.log("data",data);
    let result = await HttpClient.requestData("servicecart", "POST", data);
    console.log("result", result);
    if (result && result.status) {
      window.history.pushState(null, "", "/checkoutservice");
      window.location.href = "/checkoutservice";
    } else {
      toast.error(result.message, {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  };

  const reject=()=>{
    // const updates = {};
    // updates[`/Chat/${userData._id}/messages/unseen`] = false;
    // update(ref(db), updates);
  }
  return (
    <div className="card chat_card  ">
      <ToastContainer />
      <div className="card-body">
        <div className="d-flex justify-content-between">
          <h6>{props.offername}</h6>
          <h4 className="ml-5">$ {props.price}</h4>
        </div>
        <h5>{props.details}</h5>
        <div className="text-right text-muted">
          <a href="#" className="text-muted">
            Read More
          </a>
        </div>
        <div>
          <p>
            Offer Time :<span>2 days</span>{" "}
          </p>
        </div>
        {userData.type == "Seller" ? null : (
          <div className="d-flex">
            <div>
              <button className="btn card_btn" onClick={accept}>
                Accept
              </button>
            </div>
            <div className="mx-3">
              <button className="btn card_btn bg2" onclick={reject}>Reject</button>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};
export default MessageBody;
