import React, { useState, useEffect } from "react";
import Details from "./Details";
import Highlight from "./Highlight";
import ReviewSec from "./ReviewSec";
import Pagination from "../Components/Pagination";
import Loader from "../../Component/loader";
import OtherService from "../Components/OtherService";
import Footer from "../../Defaults/Footer";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { Link } from "react-router-dom";
import { ReactTitle } from "react-meta-tags";
import RelatedService from "./RelatedService";
import HttpClient from "../../utils/HttpClient";
const Index = (props) => {
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    window.scrollTo(0, 0);
    fetchService();
  }, []);
  var settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };

  const id = props.match.params.id;

  const [service, setService] = useState({});
  const [reviewlist, setreviewlist] = useState([]);

  const [reviewcount, setreviewcount] = useState(0);

  const [salescount, setsalescount] = useState(0);
  const [catid, setcatid] = useState("");

  const fetchService = async () => {
    // console.log("idd", id);
    setLoading(true);
    let result = await HttpClient.requestData(
      "shop/view-shopservice/" + id,
      "GET"
    );
    console.log("servicefetchsingle", result);
    if (result && result.status) {
      getreview(result.data[0]._id);
      getsalescount(result.data[0]._id);
      setService(result.data[0]);
      setcatid(result.data[0].category_id);
      setTimeout(() => {
        setLoading(false);
      }, 1);
    } else {
    }
  };

  const getreview = async (id) => {
    // setLoading(true);
    let sendData = {};
    console.log("object", sendData);
    let result = await HttpClient.requestData(
      "servicereview/" + id,
      "GET",
      sendData
    );
    console.log("review", result);
    if (result && result.status) {
      setreviewlist(result.data);
      setreviewcount(result.data.length);
      // let sum = 0;
      // let avg = 0;
      // result.data.map((itemm, index) => {
      //   return (sum = Number(sum) + Number(itemm.rating));
      // });
      // avg = Number(sum) / Number(result.data.length);
      // setavg(avg);
    } else {
    }
  };

  const getsalescount = async (id) => {
    // setLoading(true);
    let sendData = {};
    console.log("object", sendData);
    let result = await HttpClient.requestData(
      "sales-count/" + id,
      "GET",
      sendData
    );
    // console.log("sales-count", result.number);
    if (result && result.status) {
      setsalescount(result.number);
    } else {
    }
  };
  return (
    <div>
      <div className="wrapper">
        <ReactTitle title="Astrophy-Service Details" />
      </div>
      {loading ? (
        <Loader />
      ) : (
        <>
          <div className="container-fluid">
            <div className="row py-4 px-md-5">
              <div className="col-md-12 back_to">
                <Link to="/services">
                  <p>
                    {" "}
                    <i className="fas fa-chevron-left mr-2"></i>Back to Services
                  </p>
                </Link>
              </div>
            </div>
          </div>
          <section>
            <div className="container-fluid">
              <Details
                service={service}
                reviewcount={reviewcount}
                salescount={salescount}
              />
            </div>
          </section>
          <section>
            <div className="container-fluid">
              <div className="row p-md-4">
                <div className="col-md-12">
                  <Highlight service={service} />
                </div>
              </div>
            </div>
          </section>
          <section>
            <div className="container-fluid">
              <div className="row p-md-4">
                <div className="col-md-12">
                  <ReviewSec data={reviewlist} reviewcount={reviewcount} />
                </div>
              </div>
            </div>
          </section>
          <section className="container">
            <div className="row">
              <div className="col-md-12 ">{/* <Pagination /> */}</div>
            </div>
          </section>
          <section style={{ position: "relative" }}>
            <div className="container-fluid ">
              <div className="row p-md-4">
                <div className="col-md-12 Related">
                  <h4>Related Services</h4>
                </div>
              </div>
              <RelatedService service_id={id} catid={catid} />
            </div>
          </section>
          <section>
            <div className="container-fluid">
              <div className="row">
                <div className="col-md-12 my-5">
                  <div className="service_head">
                    <h6>Reading</h6>
                    <h3 className="my-4">Other Services</h3>
                  </div>
                </div>
              </div>
              <OtherService />
            </div>
          </section>{" "}
          <section>
            <Footer />
          </section>
        </>
      )}
    </div>
  );
};
export default Index;
