import React, { useState, useEffect } from "react";
import Casting from "./Casting/Casting";
import Provider from "./Provider/Provider";
import Service from "./Service/Service";
import Banner from "./Banner/Banner";
import Provider1 from "../../Assets/Images/provider1.png";
import Provider2 from "../../Assets/Images/provider2.png";
import Provider3 from "../../Assets/Images/provider3.png";
import Provider4 from "../../Assets/Images/provider4.png";
import Provider2_img1 from "../../Assets/Images/provider2img1.png";
import Provider2_img2 from "../../Assets/Images/provider2img2.png";
import Provider2_img3 from "../../Assets/Images/provider3img3.png";
import Provider2_img4 from "../../Assets/Images/provider2img4.png";
import Product from "./Products/Product";
import ProductUpdated from "./Products/ProductUpdated";
import Blog from "./Blog/Blog";
import BlogUpdated from "./Blog/BlogUpdated";
import Faq from "./Faq/Faq";
import Footer from "../../Defaults/Footer";
import Product1_img from "../../Assets/Images/thought-catalog-o0Qqw21-0NI-unsplash 1.png";
import Product2_img from "../../Assets/Images/thought-catalog-o0Qqw21-0NI-unsplash 1 (1).png";
import Product3_img from "../../Assets/Images/thought-catalog-o0Qqw21-0NI-unsplash 1 (2).png";
import Product4_img from "../../Assets/Images/thought-catalog-o0Qqw21-0NI-unsplash 1 (3).png";
import Product5_img from "../../Assets/Images/thought-catalog-o0Qqw21-0NI-unsplash 1 (4).png";
import Product6_img from "../../Assets/Images/thought-catalog-o0Qqw21-0NI-unsplash 1 (5).png";
import { ReactTitle } from "react-meta-tags";
import TopIntro from "../Components/topIntro";
import HttpClient from "../../utils/HttpClient";
import { Link } from "react-router-dom";
import { reactLocalStorage } from "reactjs-localstorage";
import { confirmAlert } from "react-confirm-alert"; // Import
import "react-confirm-alert/src/react-confirm-alert.css"; // Import css
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
var id = "";
const loginstatus = reactLocalStorage.getObject("loginstatus");
const Index = () => {
  useEffect(() => {
    window.scrollTo(0, 0);
    getproduct();
    getprovider();
    getSpellCastingData();
    getBlogData();
    // sortByPriorityy();
    if (loginstatus) {
      getcart();
    }
  }, []);

  const loop2 = [
    {
      img: Provider2_img1,
      name: "Lindsey Dias",
      status: "Tarot Reading",
      rating: "4.6/5 (124 sales)",
    },
    {
      img: Provider2_img2,
      name: "Jaydon Botosh",
      status: "Psychic Reading",
      rating: "4.6/5 (21sales)",
    },
    {
      img: Provider2_img3,
      name: "Emerson Stanton",
      status: "Palm Reading",
      rating: "4.6/5 (213sales)",
    },
    {
      img: Provider2_img4,
      name: "Emerson Philips",
      status: "Picture Reading",
      rating: "4.6/5 (124 sales)",
    },
    {
      img: Provider2_img1,
      name: "Lindsey Dias",
      status: "Tarot Reading",
      rating: "4.6/5 (124 sales)",
    },
  ];
  const [loop3, setloop3] = useState([]);

  const [providerdata, setproviderdata] = useState([]);
  const [shortedProvider, setshortedProvider] = useState([]);
  const [spellCastingData, setspellCastingData] = useState([]);
  const [blogData, setblogData] = useState([]);
  const userData = reactLocalStorage.getObject("userData");
  const getproduct = async () => {
    let sendData = {};
    // console.log("object", sendData);
    let result = await HttpClient.requestData(
      "listProducts/" + 1,
      "GET",
      sendData
    );
    console.log("listProducts", result);
    if (result && result.status) {
      setloop3(result.data.itemsList);
    } else {
    }
  };

  const getSpellCastingData = async () => {
    let result = await HttpClient.requestData("spellCasting", "GET");
    // console.log("spellCasting data--", result);
    if (result && result.status) {
      setspellCastingData(result.data);
    }
  };

  const getBlogData = async () => {
    let result = await HttpClient.requestData("Blog", "GET");
    console.log("result blog data--", result);
    if (result && result.status) {
      setblogData(result.data);
    }
  };

  const sortByPriorityy = (data) => {
    let sortByPriority = data.sort((a, b) =>
      a.priority > b.priority ? 1 : -1
    );
    let sortData = sortByPriority;
    setshortedProvider(sortData);
  };

  const getprovider = async () => {
    let sendData = {};
    // console.log("object", sendData);
    let result = await HttpClient.requestData("topProviders", "GET", sendData);
    // console.log("topProviders", result);
    if (result && result.status) {
      setproviderdata(result.data);
      sortByPriorityy(result.data);
    } else {
    }
  };

  const getcart = async () => {
    let data = {};
    // console.log("data", data);
    let result = await HttpClient.requestData(
      "servicecart/" + userData._id,
      "GET",
      data
    );
    // console.log("result", result);
    if (result && result.status) {
      let count = result.data.length;

      if (count > 0) {
        id = result.data[0]._id;
        confirmAlert({
          title: "There is item in service cart",
          message: "Do you want to delete this?",
          buttons: [
            {
              label: "Yes",
              onClick: () => confirmyes(id),
            },
            {
              label: "No",
              onClick: () => confirmno(),
            },
          ],
        });
      } else {
      }
    } else {
    }
  };

  const confirmyes = async (id) => {
    let data = {};
    let result = await HttpClient.requestData(
      "servicecart/" + id,
      "DELETE",
      data
    );
    // console.log("result", result);
    if (result && result.status) {
      toast.success("Cart Deleted!", {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
      // window.location.href = "/home";
    } else {
    }
  };

  const confirmno = () => {
    // alert("csshygdy")
    window.history.pushState(null, "", "/checkoutservice");
    window.location.href = "/checkoutservice";
  };

  const refresh = (val) => {
    if (val) {
      getproduct();
    }
  };
  return (
    <div>
      <div className="wrapper ">
        <ToastContainer />
        <ReactTitle title="Astrophy-Home" />
      </div>
      <section>
        <Banner />
      </section>
      <section>
        <div
          className="container my-md-5 my-3 home-service"
          style={{ maxWidth: "1200px" }}
        >
          <TopIntro name="Services" title="We Provide " title1="Services" />
          <Service />
          <div className="row">
            <div className="text-center col-md-12">
              <Link to="/more-services" className="btn my-4 view-btn ">
                View More
              </Link>
            </div>
          </div>
        </div>
      </section>
      <section className="Provider mt-5">
        <div className="container" style={{ maxWidth: "1200px" }}>
          <TopIntro name="Providers" title="Top Service Provider" />
          <Provider data={shortedProvider} />
        </div>
        <div className="text-center">
          <Link to="/more-provider" className="btn my-4 view-btn ">
            View More
          </Link>
        </div>
      </section>
      <section>
        <div className="container home-service" style={{ maxWidth: "1200px" }}>
          <TopIntro name="Spell" title="We offer " title1="Spell Casting" />
          <Casting spellCastingData={spellCastingData} />
          <div className="text-center">
            <Link to="/more-spell" className="btn my-4 view-btn ">
              View More
            </Link>
          </div>
        </div>
      </section>
      {/* <section className="Provider mt-5">
        <div className="container-fluid px-lg-5">
          <TopIntro name="Providers" title="Top Service Provider" />
          <Provider data={providerdata} />
        </div>
      </section> */}
      <section className="">
        <div
          className="container px-lg-0 mb-5 Products"
          style={{ maxWidth: "1200px" }}
        >
          <TopIntro name="Product" title="Product for " title1="Sell" />
          <ProductUpdated data={loop3} refresh={refresh} />
          <div className="text-center">
            <Link to="/our-products">
              <button href="#" className="btn my-4 ">
                View More
              </button>
            </Link>
          </div>
        </div>
      </section>
      <section className="Blog-- my-md-5 my-3">
        <div className="container " style={{ maxWidth: "1200px" }}>
          <TopIntro name="Blog" title="Blogs" />
          <BlogUpdated blogData={blogData} />
          <div className="text-center">
            <Link to="/more-blog" className="btn mb-5 blog_btn ">
              Read More
            </Link>
          </div>
        </div>
      </section>
      <section className="my-5">
        <div className="container" style={{ maxWidth: "1200px" }}>
          {/* <TopIntro name="FAQ" title="Frequently Asked Questions" /> */}
          <div className="row px-3">
            <div className="col-md-12 faq_head_home">
              <h6>FAQ</h6>
              <h3>
                Frequently Asked <span>Questions</span>{" "}
              </h3>
            </div>
          </div>
          <div className="row mb-5 answer px-3">
            <div className="col-md-10">{/* <Faq /> */}</div>
            <div className="col-md-2"></div>
          </div>
          <div className="row px-3">
            <div className="col-md-12">
              <Link to="/faq" className="btn mb-4 ans_btn ">
                Find More, Click here...
              </Link>
            </div>
          </div>
        </div>
      </section>
      <section>
        <Footer />
      </section>
    </div>
  );
};
export default Index;
