import React, { useState, useEffect } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Tarot from "../../Assets/Images/tarot.png";
import Numeric from "../../Assets/Images/Numerology.png";
import Astrophy from "../../Assets/Images/Astrology.png";
import Astro from "../../Assets/Images/Astro.png";
import Mind from "../../Assets/Images/Mind Reading.png";
import HttpClient from "../../utils/HttpClient";
const OtherService = () => {
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 4,

    responsive: [
      {
        breakpoint: 1124,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          initialSlide: 1,
        },
      },
      {
        breakpoint: 400,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };
  const [service, setService] = useState([]);
  useEffect(() => {
    fetchService();
  }, []);

  const fetchService = async () => {
    let result = await HttpClient.requestData("service", "GET");
    console.log("Service", result);
    if (result && result.status) {
      let data = result.data;
      console.log("data", data);
      setService(data);
      console.log("Servicedata", service);
    } else {
    }
  };
 

  return (
    <div className=" other_area">
      <Slider {...settings}>
        {service.length>0?service.map((item, index) => {
          return (
            <div className="px-3" key={index}>
              <div className="card text-center service p-2 my-3">
                <div className="card-body service_part text-center">
                  <img
                    src={"https://admin.blitzcode.online/" + item.image}
                    alt="img"
                    className="m-auto"
                  />
                  <h5 className="card-title my-3">{item.name}</h5>
                  <p className="card-text">{item.description.substring(0, 85)}</p>
                  {/* <Link to={"/services/" + item.id}> */}
                  <button
                    className="btn my-3 "
                    onClick={() =>
                      (window.location.href = "/services/" + item._id)
                    }
                  >
                    View More
                  </button>
                  {/* </Link> */}
                </div>
              </div>
            </div>
          );
        }):null}
      </Slider>
    </div>
  );
};
export default OtherService;
