import React, { useState, useEffect } from "react";
import OrderDetails from "./OrderDetails";
import OrderList from "./OrderList";
import Footer from "../../Defaults/Footer";
import { ReactTitle } from "react-meta-tags";
import { reactLocalStorage } from "reactjs-localstorage";
import HttpClient from "../../utils/HttpClient";
import Loader from "../../Component/loader";
const Index = () => {
  useEffect(() => {
    window.scrollTo(0, 0);
    getorderlist();
  }, []);
  const userData = reactLocalStorage.getObject("userData");
  const [order, setorder] = useState([]);
  const [loading, setLoading] = useState(false);
  const [orderdetails, setorderdetails] = useState({});
  const getorderlist = async () => {
    setLoading(true);
    let data = {};
    let result = await HttpClient.requestData(
      "orderdetails/" + userData._id,
      "GET",
      data
    );
    console.log("result", result);
    if (result && result.status) {
      setorder(result.data);
      setTimeout(() => {
        setLoading(false);
      }, 1);
    } else {
    }
  };

  const sendata = (val) => {
    // console.log("object", val);
    setorderdetails(val);
  };

  const refresh = (val) => {
    if (val) {
      getorderlist();
    }
  };
  return (
    <div>
      <div className="wrapper">
        <ReactTitle title="Astrophy-Orders" />
      </div>
      {loading ? (
        <Loader />
      ) : (
        <>
          <section>
            <div className="container my-2">
              <div className="row">
                <div className="col-md-12 order_head my-3">
                  <h3>My Orders</h3>
                </div>
              </div>
              <div className="row">
                <div className="col-md-4   ">
                  <OrderList order={order} sendata={sendata} />
                </div>
                <div className="col-md-8 my-md-0 my-4">
                  {Object.keys(orderdetails).length != 0 ? (
                    <div className="order_details">
                      <OrderDetails
                        orderdetails={orderdetails}
                        refresh={refresh}
                      />
                    </div>
                  ) : (
                    <div className="text-center mt-5">
                      <img
                        src="https://c1.alamy.com/thumbs/trta2g/online-shopping-icon-vector-with-male-customer-person-profile-avatar-symbol-for-buying-goods-in-glyph-pictogram-illustration-trta2g.jpg"
                        alt="img"
                        style={{
                          width: "200px",
                          height: "200px",
                          borderRadius: "50%",
                        }}
                      />
                      <h4 className="pt-3">
                        Click on the orders list there, to view order status
                      </h4>
                    </div>
                  )}
                </div>
              </div>
            </div>
          </section>
          <section>
            <Footer />
          </section>
        </>
      )}
    </div>
  );
};
export default Index;
