import React, { useState, useEffect } from "react";
import Detailsproduct from "../../Assets/Images/detailspro_pic.png";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import HttpClient from "../../utils/HttpClient";
import { reactLocalStorage } from "reactjs-localstorage";
const CreateShop = () => {
  useEffect(() => {
    getshop();
  }, []);
  const [name, setname] = useState("");

  const userdata = reactLocalStorage.getObject("userData");

  const [title, settitle] = useState("");

  const [tags, settags] = useState("");
  const [desc, setdesc] = useState("");
  const [personalization, setpersonalization] = useState("");

  const [banner, setbanner] = useState("");

  const [arr, setarr] = useState([]);

  const [shop, setshop] = useState("");

  const [shoparr, setshoparr] = useState([]);

  // const [nameerror, setnameerror] = useState(false);

  // const [titleerror, settitleerror] = useState(false);

  // const [tagserror, settagserror] = useState(false);

  // const [descerror, setdescerror] = useState(false);

  // const [setperserror, setsetperserror] = useState(false);

  const onImageId = async (event) => {
    setbanner(event.target.files[0]);
    setarr([
      {
        preview: URL.createObjectURL(event.target.files[0]),
      },
    ]);
    console.log("arr", arr);
  };

  const onShopId = async (event) => {
    setshop(event.target.files[0]);
    setshoparr([
      {
        preview: URL.createObjectURL(event.target.files[0]),
      },
    ]);
    console.log("arr", arr);
  };

  const getshop = async () => {
    let data = {};
    console.log("object", data);
    let result = await HttpClient.requestData(
      "shop/" + userdata._id,
      "GET",
      data
    );
    console.log("result", result);
    if (result && result.status) {
      setname(result.data ? result.data.name : "");
      settitle(result.data ? result.data.title : "");
      settags(result.data ? result.data.tags : "");
      setdesc(result.data ? result.data.description : "");
      setpersonalization(result.data ? result.data.personalization : "");
      result.data
        ? setarr([
            {
              preview: "https://admin.blitzcode.online/" + result.data.banner_img,
            },
          ])
        : setarr([]);
      result.data
        ? setshoparr([
            {
              preview: "https://admin.blitzcode.online/" + result.data.shop_img,
            },
          ])
        : setshoparr([]);
    } else {
    }
  };
  const save = async () => {
    if (
      name == "" ||
      title == "" ||
      tags == "" ||
      desc == "" ||
      personalization == ""
    ) {
      toast.error("Please Enter All Fields", {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    } else if (arr.length == 0 || shoparr.length == 0) {
      toast.error("Please Enter Images", {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    } else {
      // console.log("object")
      let data = new FormData();
      if (banner != "") {
        data.append("file1", banner);
      }

      if (shop != "") {
        data.append("file2", shop);
      }

      data.append("name", name);
      data.append("title", title);
      data.append("description", desc);
      data.append("userid", userdata._id);
      data.append("tags", tags);
      data.append("personalization", personalization);
      let result = await HttpClient.fileUplode("shop", "POST", data);
      console.log("result", result);
      if (result && result.status) {
        toast.success(result.message, {
          position: "bottom-right",
          autoClose: 1000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
        getshop();
      } else {
        toast.error(result.error[0].msg, {
          position: "bottom-right",
          autoClose: 1000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      }
    }
  };

  return (
    <div className="row create_shop mt-3">
      <ToastContainer />
      <div className="col-md-6 mx-auto">
        <div className="d-flex justify-content-between">
          <div>
            <h5>
              Banner Image{" "}
              <span class="required" style={{ color: "red" }}>
                *
              </span>
            </h5>
            {/* <img src={Detailsproduct} alt="img" /> */}
            {arr.map((item, index) => {
              console.log("object", item);
              return (
                <>
                  <div class="img-wrap">
                    {/* <span ></span> */}
                    {/* <button class="close">&times;</button> */}
                    <img
                      className="avatar ml-3 img-fluid img-thumbnail"
                      key={index}
                      alt="img"
                      style={{ width: "54px", height: "54px" }}
                      src={item.preview}
                    />
                  </div>
                </>
              );
            })}
          </div>

          <div className="choose_file my-4">
            <input type="file" className="input_file" onChange={onImageId} />
            Choose File
          </div>
        </div>
        <div className="d-flex justify-content-between">
          <div>
            <h5>
              Shop Image{" "}
              <span class="required" style={{ color: "red" }}>
                *
              </span>
            </h5>
            {/* <img src={Detailsproduct} alt="img" /> */}
            {shoparr.map((item, index) => {
              console.log("object", item);
              return (
                <img
                  className="avatar ml-3 img-fluid img-thumbnail"
                  key={index}
                  alt="img"
                  style={{ width: "54px", height: "54px" }}
                  src={item.preview}
                />
              );
            })}
          </div>

          <div className="choose_file my-4">
            <input type="file" className="input_file" onChange={onShopId} />
            Choose File
          </div>
        </div>
        <div>
          <div className="form-group mt-3">
            <label htmlFor="">
              Shop Name{" "}
              <span class="required" style={{ color: "red" }}>
                *
              </span>
            </label>
            <input
              type="text"
              className="form-control"
              id=""
              placeholder="Please Enter Shop Name"
              onChange={(val) => {
                if (val.target.value.match("^[a-zA-Z ]*$") != null) {
                  setname(val.target.value);
                  // if (val.target.value == "") {
                  //   setnameerror(true);
                  // } else {
                  //   setnameerror(false);
                  // }
                }
              }}
              value={name}
            />
          </div>
          <div className="form-group mt-3">
            <label htmlFor="">
              Shop Title{" "}
              <span class="required" style={{ color: "red" }}>
                *
              </span>
            </label>
            <input
              type="text"
              className="form-control"
              id=""
              placeholder="Please Enter Shop Title"
              onChange={(val) => {
                if (val.target.value.match("^[a-zA-Z ]*$") != null) {
                  settitle(val.target.value);
                  // if (val.target.value == "") {
                  //   settitleerror(true);
                  // } else {
                  //   settitleerror(false);
                  // }
                }
              }}
              value={title}
            />
          </div>
          <div className="form-group mt-3">
            <label htmlFor="">
              Tags{" "}
              <span class="required" style={{ color: "red" }}>
                *
              </span>
            </label>
            <input
              type="text"
              className="form-control"
              id=""
              placeholder="Please Enter Tags"
              onChange={(val) => {
                if (val.target.value.match("^[a-zA-Z ]*$") != null) {
                  settags(val.target.value);
                  // if (val.target.value == "") {
                  //   settagserror(true);
                  // } else {
                  //   settagserror(false);
                  // }
                }
              }}
              value={tags}
            />
          </div>
          <div className="form-group mt-3">
            <label for="exampleFormControlTextarea1">
              Description{" "}
              <span class="required" style={{ color: "red" }}>
                *
              </span>
            </label>
            <textarea
              class="form-control"
              id="exampleFormControlTextarea1"
              rows="3"
              placeholder="Please Enter Description"
              onChange={(val) => {
                if (val.target.value.match("^[a-zA-Z ]*$") != null) {
                  setdesc(val.target.value);
                  // if (val.target.value == "") {
                  //   setdescerror(true);
                  // } else {
                  //   setdescerror(false);
                  // }
                }
              }}
              value={desc}
            ></textarea>
          </div>
          <div className="form-group mt-3">
            <label for="exampleFormControlTextarea1">
              Personalization{" "}
              <span class="required" style={{ color: "red" }}>
                *
              </span>
            </label>
            <textarea
              class="form-control"
              id="exampleFormControlTextarea1"
              rows="3"
              placeholder="Please Enter Personalization"
              onChange={(val) => {
                if (val.target.value.match("^[a-zA-Z ]*$") != null) {
                  setpersonalization(val.target.value);
                  // if (val.target.value == "") {
                  //   setperserror(true);
                  // } else {
                  //   setperserror(false);
                  // }
                }
              }}
              value={personalization}
            ></textarea>
          </div>
          <div className="text-center my-3">
            <button type="button" className="btn changes_btn" onClick={save}>
              Save Changes
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};
export default CreateShop;
