import React from "react";
import { Link } from "react-router-dom";

const MorePopular = () => {
  const loop = [
    {
      date: "April 25, 2021",
      type: "true",
    },
    {
      date: "April 25, 2021",
      type: "true",
    },
    {
      date: "April 25, 2021",
      type: "true",
    },
    {
      date: "April 25, 2021",
      type: "false",
    },
  ];
  return (
    <div className="popular_box my-4 p-3  ">
      {loop.map((item, index) => {
        return (
          <div
            className={
              item.type === "true"
                ? "popular_text border-bottom pt-3"
                : "popular_text pt-3"
            }
          >
            <h6>{item.date}</h6>
            <Link to="/blog-details">
              <h5>5 ASTRONOMY BOOKS FOR BEGINNERS</h5>
            </Link>
            <Link to="/blog-details">
              {" "}
              <p>Read More</p>
            </Link>
          </div>
        );
      })}
    </div>
  );
};
export default MorePopular;
