import React, { useState, useEffect } from "react";
import Avatar from "../../Assets/Images/avatar.png";
import Activity from "../../Views/Seller/Activity";
import shop1 from "../../Assets/Images/service_list 1.png";
import Cat4 from "../../Assets/Images/cast4.png";
import { Link } from "react-router-dom";
import ReviewList from "../../Views/ServiceDetails/ReviewList";
import EditSellerProfile from "./EditSellerProfile";
import HttpClient from "../../utils/HttpClient";
import { reactLocalStorage } from "reactjs-localstorage";
import Loader from "../../Component/loader";
const Index = () => {
  const [edit, setEdit] = useState(false);
  const [user, setuser] = useState({});
  const [loading, setLoading] = useState(false);

  const [totalEarning, settotalEarning] = useState(0);

  const [totalSettle, settotalSettle] = useState(0);
  const [pending, setpending] = useState(0);
  const [wallet, setwallet] = useState(0);
  const userdata = reactLocalStorage.getObject("userData");
  useEffect(() => {
    fetchData();
    fetchcount();
  }, []);

  const fetchData = async () => {
    setLoading(true);

    let result = await HttpClient.requestData("seller/" + userdata._id, "GET");
    // console.log("Data", result);
    if (result && result.status) {
      setuser(result.data);
      setTimeout(() => {
        setLoading(false);
      }, 1);
      // console.log("setuser", user);
    } else {
    }
  };

  const fetchcount = async () => {
    let result = await HttpClient.requestData(
      "getSellersettlement/" + userdata._id,
      "GET"
    );
    console.log("Count", result);
    // if (result && result.status) {
    settotalEarning(result.total_earnings);

    settotalSettle(result.earning_settled);
    settotalSettle(result.earning_settled);
    setpending(result.pending_settlement);
    setwallet(result.claimable_earnings);
    // setuser(result.data);

    console.log("setuser", result.total_earnings);
    // } else {
    // }
  };

  const Item = [
    {
      img: shop1,
      name: "Lorem Ipsum",
      para: "Description",
      status: "500",
    },
    {
      img: shop1,
      name: "Lorem Ipsum",
      para: "Description",
      status: "500",
    },
    {
      img: shop1,
      name: "Lorem Ipsum",
      para: "Description",
      status: "500",
    },
    {
      img: shop1,
      name: "Lorem Ipsum",
      para: "Description",
      status: "500",
    },
  ];
  const loop = [1, 2, 3];
  return (
    <div className="Seller_Profile">
      {edit ? (
        <div>
          <div className="row my-2">
            <div className="col-md-12">
              <div onClick={() => setEdit(false)}>
                <a href="#" className="text-dark">
                  <i className="fas fa-chevron-left mr-2"></i>
                  Back
                </a>
              </div>
            </div>
          </div>
          <EditSellerProfile />
        </div>
      ) : loading ? (
        <Loader />
      ) : (
        <div>
          <div className="row my-3">
            <div className="col-md-8 sell_profile_head p-3 mx-auto">
              <div className="d-flex justify-content-between">
                <div className="d-flex">
                  <img src={Avatar} alt="img" />
                  <div className="px-3 py-2 ">
                    <h5>{user.firstName + " " + user.lastName}</h5>

                    <p>{user.email}</p>
                  </div>
                </div>
                <div className="" onClick={() => setEdit(true)}>
                  <button className="btn my-3" type="button">
                    <i className="fas fa-pencil-alt text-white"></i>
                  </button>
                </div>
              </div>
            </div>
          </div>
          <div className="row">
            <div className="col-md-8 mx-auto">
              <div className="row">
                <div className="col-md-6 my-2">
                  <div className="balance_card">
                    <div className="card-body text-center">
                      <h6>Total Earning</h6>
                      <span>{totalEarning}</span>
                    </div>
                  </div>
                </div>
                <div className="col-md-6 my-2">
                  <div className="balance_card">
                    <div className="card-body text-center">
                      <h6>Earning Settled </h6>
                      <span>{totalSettle}</span>
                    </div>
                  </div>
                </div>
                <div className="col-md-6 my-2">
                  <div className="balance_card">
                    <div className="card-body text-center">
                      <h6>Pending </h6>
                      <span>{pending}</span>
                    </div>
                  </div>
                </div>
                <div className="col-md-6 my-2">
                  <div className="balance_card">
                    <div className="card-body text-center">
                      <h6>Claimable Amount</h6>
                      <span>{wallet}</span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          {/* <div className="row my-4">
            <div className="col-md-8 offset-md-2">
              <Activity data={Item} />
            </div>
          </div> */}
          {/* <div className="row">
            <div className="col-md-8 offset-md-2">
              <div className="d-flex justify-content-between">
                <h4>User Review</h4>
                <div className="">
                  <Link to="/..">View More</Link>
                </div>
              </div>
              <ReviewList data={loop} />
            </div>
          </div> */}
        </div>
      )}
    </div>
  );
};
export default Index;
