import React from "react";
import "rc-slider/assets/index.css";

const EditBudgetModal = (props) => {
  const Slider = require("rc-slider");
  const createSliderWithTooltip = Slider.createSliderWithTooltip;
  const Range = createSliderWithTooltip(Slider.Range);

  const slider = () => {
    console.log("");
  };
  return (
    <div>
      <div className="modal-dialog  my-3 ">
        <div className="modal-content dlt_modal">
          <button
            type="button"
            className="cross close ml-auto px-4 py-2"
            data-dismiss="modal"
            aria-label="Close"
            onClick={() => props.callBackBudget(false)}
          >
            <span aria-hidden="true"></span>
          </button>
          <div className="login-body mb-3 p-5 ">
            <div className="text-center h4 pb-4">Edit Your Price Range</div>
            <Range
              min={100}
              max={20000}
              defaultValue={[3000, 15000]}
              tipFormatter={(value) => `$${value}`}
              marks={{ 100: 100, 20000: 20000 }}
              onChange={slider}
            />
            <div className="text-right mt-5">
              <button
                className="btn budget_btn"
                onClick={() => props.callBackBudget(false)}
              >
                Submit
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default EditBudgetModal;
