import React, { useState, useEffect } from "react";
import BlankOrder from "../Orders/BlankOrder";
import { reactLocalStorage } from "reactjs-localstorage";
import HttpClient from "../../utils/HttpClient";
import moment from "moment";
import RefundModal from "./refundmodal";
import { Row, Card, CardBody, Col, Input, Button } from "reactstrap";
const ServiceOrderList = (props) => {
  const [isOpenmodal, setisopenmodal] = useState(false);
  const [isloading, setisloading] = useState(false);
  const [sentorderdetails, setsentorderdetails] = useState({});
  useEffect(() => {

  }, []);

  const orderdertail = (item) => {
    console.log("object", item);

    props.sendata(item);

  };

  const Callbackmsg = async (val) => {

    setisopenmodal(false);
    props.updatedList(props.order, 2)


  };

  // const callrefund = async (item) => {
  //   let result = await HttpClient.requestData("refundProduct/" + reactLocalStorage.getObject("userData")._id, "PUT");
  //   console.log("result1", result);
  //   if (result && result.status) {
  //   }
  // }

  return (
    <div className="order_list">
      <div className="row">
        {props.order.length > 0 ? (
          props.order.map((item, index) => {
            return (
              <div
                className="col-md-12 "
                key={index}
                onClick={() => {
                  orderdertail(item)
                  setsentorderdetails(item);
                }
                }
              >
                <div className="border-bottom p-3 highlight">
                  <h5>Order Number : {item.order_id}</h5>
                  <div className="d-flex justify-content-between">
                    <p>
                      <span>
                        Order On:{" "}
                        {moment(item.booking_date).format("D-MM-YYYY")}
                      </span>
                      <br></br>
                      { }
                    </p>
                    <p>
                      <span>Total Amount:</span>
                      <br></br> {item.total}
                    </p>

                  </div>
                  {item.completestatus ?
                    <p>
                      <Button
                        type="button "
                        color={item.servicerefund_data.length > 0 && item.servicerefund_data[0].admin_status == "refund_initiated" ? "success" :
                          item.servicerefund_data.length > 0 && item.servicerefund_data[0].admin_status == "refund_rejected" ? "secondary" :
                            item.servicerefund_data.length > 0 && item.servicerefund_data[0].admin_status == "pending" ? "warning" :
                              "info"}
                        onClick={() => {

                          // setTimeout(() => {
                          setisopenmodal(!isOpenmodal);
                          // }, 1000);
                          // setTimeout(() => {
                          console.log("hhj", isOpenmodal);
                          // }, 1000);

                        }}
                      >
                        {item.servicerefund_data.length > 0 &&
                          item.servicerefund_data[0].admin_status == "refund_initiated" ?
                          "Refund Initiated" :
                          item.servicerefund_data.length > 0 && item.servicerefund_data[0].admin_status == "refund_rejected" ? "Refund Rejected" :
                            item.servicerefund_data.length > 0 && item.servicerefund_data[0].admin_status == "pending" ? "Refund Requested" :
                              "Refund"
                        }
                      </Button>
                    </p> : null}
                </div>
              </div>
            );
          })
        ) : (
          <BlankOrder />
        )}
      </div>
      {isOpenmodal ? (
        <RefundModal

          status={true}
          orderdetails={sentorderdetails}

          onCloseCallBack={Callbackmsg}
          image=""
        />
      ) : null}
    </div>
  );
};
export default ServiceOrderList;
