import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom';
import Footer from '../../Defaults/Footer';
import HttpClient from "../../utils/HttpClient";


const MoreProvider=()=> {

  const [providerdata, setproviderdata] = useState([]);
  const [shortedProvider, setshortedProvider] = useState([]);

  useEffect(() => {
    window.scrollTo(0, 0);
    getprovider();
  }, []);

  const sortByPriorityy = (data) => {
    let sortByPriority = data.sort((a, b) =>
      a.priority > b.priority ? 1 : -1
    );
    let sortData = sortByPriority;
    setshortedProvider(sortData);
  };
  const getprovider = async () => {
    let sendData = {};
    // console.log("object", sendData);
    let result = await HttpClient.requestData("topProviders", "GET", sendData);
    // console.log("topProviders", result);
    if (result && result.status) {
      setproviderdata(result.data);
      sortByPriorityy(result.data);
    } else {
    }
  };
    const loop=[1,2,3,4,5,6]
    return (
        <div>
        <div className="provider_sec">
            <div className="container-fluid">
                <div className="row my-4">
                    <div className="col-md-12 text-center">
                        <h1>Service Providers</h1>
                    </div>
                </div>
            
        <div className="row px-4">
         
            {shortedProvider.map((item, index) => {
              return (
                  <div className="col-md-3 my-4">
                <div className="card slider2_part px-3 py-4 " key={index}>
                  <div className="card-body slider2_body text-center p-0">
                    <img
                     src={
                      item.image  ? "https://admin.blitzcode.online/" + item.image : "https://t4.ftcdn.net/jpg/02/07/87/79/360_F_207877921_BtG6ZKAVvtLyc5GWpBNEIlIxsffTtWkv.jpg"
                    }
                      
                      alt="img1"
                      className="m-auto"
                    />
  
                    <h5 className="card-title my-3 ">
                    {item.firstName +
                      " " +
                      item.lastName}
                    </h5>
                    {/* <p className="card-text">{item.category_data.name}</p> */}
                    <div className="d-flex justify-content-center mt-2 mb-3">
                      <i className="fas fa-star my-1 mr-2"></i>
                      <p>{item.avgRating ? item.avgRating.toFixed(1) : "0"}</p>
                    </div>
                    <Link to={"/service-provider/" + item._id}>
                      <button href="#" className="btn">
                        View More
                      </button>
                    </Link>
                  </div>
                </div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
      <section>
          <Footer/>
      </section>
      </div>
    )
}

export default MoreProvider;
