// import React, { useEffect } from "react";
import Provider_banner from "../../Assets/Images/provider_banner.png";
import provider_card_img from "../../Assets/Images/provider_card_img.png";
import Provider_name from "../../Assets/Images/provider_name.png";
import contact_logo from "../../Assets/Images/Vector (6).png";
import { reactLocalStorage } from "reactjs-localstorage";
import moment from "moment";
import HttpClient from "../../utils/HttpClient";

import { useEffect, useState } from "react";
const ProviderBanner = (props) => {
  useEffect(() => {
    getdetails();
  }, []);

  const [servicedetails, setservicedetails] = useState([]);
  const details = props.details;
  console.log("details",details);
  const userData = reactLocalStorage.getObject("userData");

  const idd =
    Object.keys(details).length != 0 ? details.shop_data.userid : "";

  const shop_id = Object.keys(details).length != 0 ? details.shop_data._id : "";

  const date =
    Object.keys(details).length != 0 ? details.shop_data.start : "";

  const getdetails = async () => {
    let sendData = {};
    console.log("object", sendData);
    let result = await HttpClient.requestData(
      "shop/allprovidedServiceList/" + shop_id,
      "GET",
      sendData
    );
    console.log("shop/allprovidedServiceList/", result);
    if (result && result.status) {
      setservicedetails(result.data);
    } else {
    }
  };

  // const redirect = (id) => {
  //   alert("tp")
  //   window.location.href = "/service-details/" + id;
  // };
  return (
    <div>
      <div className="Provider_banner">
        {/* {Object.keys(details).length != 0 ? (
          <img
            src={"https://admin.blitzcode.online/" + details.shop_details.banner_img}
            alt="img"
          />
        ) : ( */}
        <img src={Provider_banner} alt="img" />
        {/* )} */}
      </div>
      <div className="container provider_banner_card">
        <div className="row">
          <div className="col-md-12">
            <div className="card">
              <div className="card-body">
                <div className="d-md-flex">
                  <div className="provider_card_img">
                    {Object.keys(details).length != 0 ? (
                      <img
                        src={
                          "https://admin.blitzcode.online/" +
                          details.shop_data.shop_img
                        }
                        alt="img"
                      />
                    ) : (
                      <img src={provider_card_img} alt="img" />
                    )}
                  </div>
                  <div className="provider_card_head px-3">
                    <div className="d-flex flex-wrap mb-2 ">
                      <p className="my-2">
                        <i className="fas fa-star icon  mr-2"></i>
                        {Object.keys(details).length != 0
                          ? details.avgRating
                          : "0"}
                        /5
                      </p>
                      {/* <button
                        className="btn provider_shop_btn mx-2"
                        type="button"
                      >
                        <i className="far fa-heart"></i> Favourite Shop (2.5K)
                      </button> */}
                      {/* <button
                        className="btn provider_shop_btn mt-md-0 mt-3"
                        type="button"
                      >
                        <i className="fas fa-shopping-cart"></i>{" "}
                        {Object.keys(details).length != 0
                          ? details.cart_data.length
                          : 0}{" "}
                        Sales
                      </button> */}
                    </div>

                    <h4>
                      {Object.keys(details).length != 0
                        ? details.shop_data.name
                        : null}
                    </h4>
                    <span>{details.firstName + " " + details.lastName}</span>
                    <p>
                      <i className="fas fa-map-marker-alt location mr-2"></i>
                      {Object.keys(details).length != 0
                        ? details.city
                        : ""}
                    </p>
                    <div className="form-group">
                      <select
                        className="form-control"
                        id="exampleFormControlSelect1"
                      >
                        <option>Provided Services</option>
                        <hr></hr>
                        {/* <option>Tarot Reading</option>
                        <option>Numerology</option>
                        <option>Astrology</option> */}
                        {servicedetails.length > 0 &&
                          servicedetails.map((item, index) => {
                            return (
                              <option
                                key={index}
                                // onClick={() => redirect(item._id)}
                              >
                                {item.name}
                              </option>
                            );
                          })}
                      </select>
                    </div>
                  </div>
                  <div className="provider_card_right ml-auto">
                    <p className="text-right">
                      Last updated on {moment(date).format("MMMM Do YYYY")}
                    </p>
                    <div className="d-flex flex-wrap details_sub_text">
                      <div className="d-flex border-left px-2">
                        <img
                          src={
                            Object.keys(details).length != 0
                              ? "https://admin.blitzcode.online/" +
                                details.image
                              : Provider_name
                          }
                          alt="img"
                          style={{
                            width: "100%",
                            height: "27px",
                            maxWidth: "27px",
                          }}
                        />
                        <div className=" mx-2">
                          <h5>
                            {Object.keys(details).length != 0
                              ? details.firstName +
                                " " +
                                details.lastName
                              : null}
                          </h5>
                          <p>
                            Ownerof{" "}
                            {Object.keys(details).length != 0
                              ? details.shop_data.name
                              : null}
                          </p>
                        </div>
                      </div>

                      {/* {Object.keys(userData).length != 0 ? (
                        userData._id != idd ? (
                          <div className="contact">
                            <img
                              src={contact_logo}
                              alt="img"
                              className="mx-3"
                            />
                            <span>Contact</span>
                          </div>
                        ) : null
                      ) : null} */}
                    </div>
                  </div>
                </div>
                <div className="provider_card_text">
                  <h5>About Shop</h5>

                  <p>
                    {Object.keys(details).length != 0
                      ? details.shop_data.personalization
                      : ""}
                  </p>
                  {/* <h6 className="text-right">Read More</h6> */}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default ProviderBanner;
