import React, { useState, useEffect } from "react";
import BlankOrder from "../../Views/Orders/BlankOrder";
import moment from "moment";
import HttpClient from "../../utils/HttpClient";
import { ToastContainer, toast } from "react-toastify";
import { Row, Card, CardBody, Col, Input, Button } from "reactstrap";
import ChatWithPersonel from "./chatwithpersonel";
import { getDatabase, ref, push, set, onValue, get, onChildAdded } from "firebase/database";
import db from "../../Firebase/fbconfig";
const OpenOrderList = (props) => {
  const [isOpenmodal, setisopenmodal] = useState(false);
  const [orderid, setorderid] = useState("");
  const [firebaseData, setfirebaseData] = useState([]);

  console.log("props.order", props.order);
  useEffect(() => {
    checklist()
  }, []);

  const checklist = async () => {
    const dbRef = getDatabase();
    const starCountRef = ref(dbRef, 'PersonelSellerchat/');
    onValue(starCountRef, (snapshot) => {
      const data = snapshot.val();
      let arr = [];
      console.log("data", data);
      if (snapshot.exists()) {
        snapshot.forEach((ele) => {
          console.log("ele", ele.val());
          let dom = {
            id: ele.val(),

          };
          console.log(dom);
          arr.push(dom);
          console.log("arr", arr);
        });
        setfirebaseData(arr);
      }
    });
  }

  const orderdertail = (item) => {
    // console.log("object",item);
    props.sendata(item);

  };

  const viewOrderDetail = async (id) => {
    setTimeout(() => {
      window.location.href = "/view-order-details/" + id;
    }, 10);
  }
  const handleAccept = async (id) => {

    let result = await HttpClient.requestData("complete-service-request/" + id, "PUT");
    console.log("status", result);
    if (result && result.status) {
      toast.success("Service Completed successfully", {
        position: "bottom-right",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
      // getorderlist();
      props.callback(props.order)
    }
    else {
      toast.error("Error", {
        position: "bottom-right",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  };

  const Callbackmsg = async (val) => {

    setisopenmodal(false);

  };

  return (
    <div className="order_list">
      <div className="row">
        {props.order.length > 0 ? (
          props.order.map((item, index) => {
            return (
              <div
                className="col-md-12 "
                key={index}
              //onClick={() => viewOrderDetail(item._id)}
              // onClick={() => orderdertail(item)}
              >

                <div className="border-bottom p-3 highlight">
                  <h5>Order Number : {item.order_id}</h5>
                  <div className="d-flex justify-content-between">
                    <p>
                      <span>
                        Order On:{" "}
                        {moment(item.booking_date).format("D-MM-YYYY")}
                      </span>
                      <br></br>
                      { }
                    </p>

                    <p>
                      <span>Total Amount:</span>
                      <br></br> {item.total}
                    </p>
                    <div>
                      <Button
                        color="primary"
                        type="button"

                        onClick={() => viewOrderDetail(item._id)}

                      >
                        View Details
                      </Button>
                    </div>
                    <div>

                      <Button
                        color={item.completestatus ? "success" : "primary"}
                        type="button"

                        onClick={() => {
                          if (item.completestatus == true) {

                          }
                          else {
                            handleAccept(item._id)
                          }
                        }
                        }

                      >
                        {item.completestatus ? "Service Completed" : "Complete Service"}
                      </Button>
                    </div>
                    <div className="_btn_width">
                      {firebaseData.length > 0 && firebaseData.map((it, index) => {
                        console.log("firebaseid", it.id.order_id);
                        console.log("uu1", item.order_id);
                        if (it.id.order_id == item.order_id) {
                          return (
                            <Button
                              color="info"
                              type="button"


                              onClick={() => {
                                setisopenmodal(!isOpenmodal);
                                setorderid(item.order_id)
                              }
                              }

                            >
                              Chat with Admin
                            </Button>
                          )
                        }
                      })
                      }

                    </div>
                  </div>
                </div>
                {isOpenmodal ? (
                  <ChatWithPersonel

                    status={true}
                    // orderdetails={sentorderdetails}
                    orderid={orderid}
                    onCloseCallBack={Callbackmsg}
                    image=""
                  />
                ) : null}
              </div>
            );
          })
        ) : (
          <BlankOrder />
        )}
      </div>
    </div >
  );
};
export default OpenOrderList;
