import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Product1_img from "../../Assets/Images/thought-catalog-o0Qqw21-0NI-unsplash 1.png";
import Product2_img from "../../Assets/Images/thought-catalog-o0Qqw21-0NI-unsplash 1 (1).png";
import Product3_img from "../../Assets/Images/thought-catalog-o0Qqw21-0NI-unsplash 1 (2).png";
import { Link } from "react-router-dom";
const RelatedProduct = () => {
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 4,

    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          initialSlide: 2,
        },
      },
      {
        breakpoint: 400,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };
  const loop = [
    {
      img: Product1_img,
      name: "Parad Meru Shri Shree Yantra",
    },
    {
      img: Product2_img,
      name: "Zodiac Sign Bracelet",
    },
    {
      img: Product3_img,
      name: "Yelook 100% Natural",
    },
    {
      img: Product1_img,
      name: "Parad Meru Shri Shree Yantra",
    },
  ];
  return (
    <div className="related_products  ">
      <Slider {...settings}>
        {loop.map((item, index) => {
          return (
            <div className="px-3" key={index}>
              <div className="card service_list Product_list ">
                <div>
                  <img
                    src={item.img}
                    className="card-img-top"
                    alt="img"
                    className="relative"
                    style={{ width: "100%", height: "100%" }}
                  />
                </div>

                <div className="card-body  product_list_text my-2">
                  <h6>{item.name}</h6>
                  <p className="card-text py-3"> 00.00</p>
                  <div className="d-flex justify-content-between ">
                    <div className="rating">
                      {" "}
                      <i className="fas fa-star my-1 mr-2"></i>
                      4.6/5
                    </div>
                    <Link to="/add-cart">
                      {" "}
                      <div className="cart">+ Add to Cart</div>
                    </Link>
                  </div>
                </div>
              </div>
            </div>
          );
        })}
      </Slider>
    </div>
  );
};
export default RelatedProduct;
