import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Favourite from "../../Assets/Images/favourite.png";
import { Link } from "react-router-dom";
import { reactLocalStorage } from "reactjs-localstorage";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import HttpClient from "../../utils/HttpClient";
import EmptyProduct from "../EmptyProduct/EmptyProduct";
const ProductList = (props) => {
  // console.log("ppp", props.data);
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };
  const loop = [1];
  const userData = reactLocalStorage.getObject("userData");
  const styles = {
    color: "green",
  };

  const loginstatus = reactLocalStorage.getObject("loginstatus");
  const addcart = async (val) => {
    let data = {
      prod_id: val._id,
      user_id: userData._id,
      productname: val.name,
      qty: 1,
      price: val.selling_price,
      image: val.image[0],
    };
    // console.log("data", data);
    let result = await HttpClient.requestData("add-to-cart", "POST", data);
    // console.log("result", result);
    if (result && result.status) {
      toast.success(result.message, {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    } else {
      toast.error(result.message, {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  };

  const alert = () => {
    toast.error("You have to login first", {
      position: "bottom-right",
      autoClose: 1000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    });
  };
  return (
    <div className="row px-0">
      <ToastContainer />
      {props.data.length > 0 ? (
        props.data.map((item, index) => {
          let sum = 0;
          let avg = 0;
          item.review_data.map((itemm, index) => {
            return (sum = Number(sum) + Number(itemm.rating));
          });
          // console.log("sum", sum);
          avg = Number(sum) / Number(item.review_data.length);
          return (
            <div className="col-lg-4 col-md-6 my-3" key={index}>
              <div className="card service_list">
                <Link to={"/product-details/" + item._id}>
                  <Slider {...settings}>
                    {loop.map((item2, index2) => {
                      return (
                        <div key={index2}>
                          {" "}
                          <img
                            src={
                              "https://admin.blitzcode.online/" + item.image[0]
                            }
                            alt="img"
                            className="relative service_list_img"
                          />
                          <img
                            src={Favourite}
                            alt="img"
                            className="absolute m-2"
                          />
                        </div>
                      );
                    })}
                  </Slider>
                </Link>
                <div className="card-body product_list_text pt-2">
                  <Link to={"/product-details/" + item._id}>
                    <h6>{item.name}</h6>
                  </Link>
                  <div className="d-flex py-3">
                    <del className="text-muted">$ {item.mrp}</del>
                    <p className="card-text px-3">$ {item.selling_price}</p>
                    <h5 className=" text-muted">(50% Offer)</h5>
                  </div>

                  <div className="d-flex justify-content-between align-items-center">
                    <div className="rating">
                      {" "}
                      <i className="fas fa-star my-1 mr-2"></i>
                      {item.avgRating ? item.avgRating.toFixed(1) : "0"}/5
                    </div>
                    {/* <Link to="/add-cart"> */}
                    {loginstatus ? (
                      typeof item.cart_details !== "undefined" &&
                      item.cart_details.length > 0 ? (
                        <button className="cart btn " style={styles}>
                          Added To Cart
                        </button>
                      ) : (
                        <button
                          className="cart btn "
                          onClick={() => addcart(item)}
                        >
                          + Add to Cart
                        </button>
                      )
                    ) : (
                      <button
                        className="cart btn"
                        // onClick={() => addcart(item)}
                        onClick={alert}
                      >
                        + Add to Cart
                      </button>
                    )}
                    {/* </Link> */}
                  </div>
                </div>
              </div>{" "}
            </div>
          );
        })
      ) : (
        <EmptyProduct />
      )}
    </div>
  );
};
export default ProductList;
