import React, { useState, useEffect } from "react";
import EditBudgetModal from "./EditBudgetModal";
import HttpClient from "../../utils/HttpClient";
import { reactLocalStorage } from "reactjs-localstorage";

const Filter = (props) => {
  const [budget, setBudget] = useState(false);
  const userData = reactLocalStorage.getObject("userData");
  const callBackBudget = (val) => {
    setBudget(val);
  };
  useEffect(() => {
    fetchService();
  }, []);
  const [service, setService] = useState([]);

  const [price, setprice] = useState("");

  const [name, setname] = useState("");

  const [id, setid] = useState("");

  const [key, setkey] = useState("");

  const [search, setsearch] = useState("");

  const [options, setoptions] = useState([
    { label: "Best Selling", value: "bestselling" },
    { label: "New Arrivals", value: "newarrivals" },
    { label: "Top Customer Reviews", value: "highlowrev" },
    { label: "Total Sales : Low to High", value: "lowhigh" },
    { label: "Total Sales : High to Low", value: "highlow" },
    { label: "Price : High to Low", value: "highlowsell" },
    { label: "Price : Low to High", value: "lowhighsell" },
    { label: "Rating : High to Low", value: "highlowrev" },
  ]);

  const [servicearr, setservicearr] = useState([]);
  const fetchService = async () => {
    let result = await HttpClient.requestData("service", "GET");
    // console.log("Service", result);
    if (result && result.status) {
      let data = result.data;
      // console.log("data",data);
      setService(data);
      // console.log("Servicedata", service);
    } else {
    }
  };

  const fetchServicee = async (id) => {
    console.log("idd", id);

    let result = await HttpClient.requestData(
      "service/shop-services/" + id,
      "GET"
    );
    console.log("servicefetchbycategory", result);
    if (result && result.status) {
      let data = result.data.itemsList.filter(
        (item) => item.shop_details[0].userid != userData._id
      );
      // console.log("data", data);

      setservicearr(data);

      // console.log("Servicedata", service);
    } else {
    }
  };

  const handleservice = (event) => {
    console.log("object", event.target.value);

    setid(event.target.value);

    fetchServicee(event.target.value);
  };

  const handleprice = async (event) => {
    setprice(event.target.value);

    let min = 0;
    let max = 0;

    if (event.target.value == "under10") {
      min = 0;
      max = 10;
    } else if (event.target.value == "under10-25") {
      min = 10;
      max = 25;
    } else if (event.target.value == "above50") {
      min = 50;
      max = 10000;
    } else {
      min = 25;
      max = 50;
    }
    let data = {
      servicename: name,
      category_id: id,
      min: min,
      max: max,
    };
    console.log(data);
    let result = await HttpClient.requestData("searchSevice", "POST", data);
    console.log("service", result);
    if (result && result.status) {
      props.filer(result.status, result.data);
    } else {
      props.filer(result.status, result.data);
    }
  };

  const handlename = async (event) => {
    setname(event.target.value);

    let min = 0;
    let max = 0;

    if (price == "under10") {
      min = 0;
      max = 10;
    } else if (price == "under10-25") {
      min = 10;
      max = 25;
    } else if (price == "above50") {
      min = 50;
      max = 10000;
    } else {
      min = 25;
      max = 50;
    }
    let data = {
      servicename: event.target.value,
      category_id: id,
      // min: min,
      // max: max,
    };
    console.log(data);
    let result = await HttpClient.requestData("searchSevice", "POST", data);
    console.log("service", result);
    if (result && result.status) {
      props.filer(result.status, result.data);
    } else {
      props.filer(result.status, result.data);
    }
  };

  const sortchoose = async (event) => {
    let data = {
      shortby: event.target.value,
    };

    console.log(data);
    let result = await HttpClient.requestData("searchSevice", "POST", data);
    console.log("service", result);
    if (result && result.status) {
      props.filer(result.status, result.data);
    } else {
      props.filer(result.status, result.data);
    }
  };

  const searchbyname = async () => {
    let min = 0;
    let max = 0;

    if (price == "under10") {
      min = 0;
      max = 10;
    } else if (price == "under10-25") {
      min = 10;
      max = 25;
    } else if (price == "above50") {
      min = 50;
      max = 10000;
    } else {
      min = 25;
      max = 50;
    }
    let data = {
      providername: search,
      // min: min,
      // max: max,
    };

    console.log(data);
    let result = await HttpClient.requestData("searchSevice", "POST", data);
    console.log("service", result);
    if (result && result.status) {
      props.filer(result.status, result.data);
    } else {
      props.filer(result.status, result.data);
    }
  };
  return (
    <div className="d-lg-flex justify-content-between py-4 filter_part">
      <div className="d-lg-flex">
        <div className=" mx-3">
          <div className="form-group">
            <select
              className="form-control"
              id="exampleFormControlSelect1"
              onClick={handleservice}
            >
              <option className="option_sec" value="">
                Select Service Category
              </option>
              <hr></hr>

              {service.map((item, index) => {
                return <option value={item._id}>{item.name}</option>;
              })}
            </select>
          </div>
        </div>
        <div className=" mx-3">
          <div className="form-group">
            <select
              className="form-control"
              id="exampleFormControlSelect1"
              onChange={handlename}
            >
              <option className="option_sec ">Select Services</option>
              <hr></hr>

              {servicearr.map((item, index) => {
                return <option value={item.name}>{item.name}</option>;
              })}
            </select>
          </div>
        </div>
        <div className=" mx-3">
          <div className="form-group">
            <select
              className="form-control"
              id="exampleFormControlSelect1"
              onChange={handleprice}
            >
              <option className="option_sec">Select Price</option>
              <hr></hr>
              <option value="under10"> $ 0 - $ 50</option>
              <option value="under10-25">$ 51 - $ 100 </option>
              <option value="under25-50"> $ 101 - $ 150 </option>
              <option value="above50">more then $ 151</option>
            </select>
          </div>
        </div>
        <div className=" mx-3">
          <div className="form-group">
            <select
              className="form-control"
              id="exampleFormControlSelect1"
              onChange={handleprice}
            >
              <option className="option_sec">Rating</option>
              <hr></hr>
              <option value="under10"> Hight to low</option>
              <option value="under10-25">low to high</option>
            </select>
          </div>
        </div>
        <div className="mx-3">
          {" "}
          {/* <button
            type="button"
            className="btn budget_btn"
            onClick={() => setBudget(true)}
          >
            Edit Price
          </button> */}
        </div>
      </div>
      <div className="px-3">
        <form className="d-flex  service-search my-4 my-md-0">
          <input
            className="form-control border-0"
            type="search"
            placeholder="Search Service Provider"
            aria-label="Search"
            value={search}
            onChange={(val) => setsearch(val.target.value)}
          />
          <span>
            {" "}
            <button className="btn" type="button" onClick={searchbyname}>
              <i className="fas fa-search  align-self-center "></i>
            </button>
          </span>
        </form>
      </div>
      <div>
        <div className="">
          <div className="form-group d-flex">
            <h6 className="mr-3 my-2">SortBy:</h6>
            <select
              className="form-control"
              id="exampleFormControlSelect1"
              onChange={sortchoose}
            >
              <option>Services Sorting</option>
              <hr></hr>
              {options.map((item, index) => {
                return <option value={item.value}>{item.label}</option>;
              })}
            </select>
          </div>
          {props.page === "services" ? (
            <p className="mb-0 mb-n3 mt-1 small text-black-50 text-right">
              1000 Results
            </p>
          ) : null}
        </div>
      </div>
      <div
        className={budget ? "modal fade loginmodal show" : "modal fade"}
        id="exampleModal"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
        style={{ display: budget ? "block" : "none" }}
      >
        <EditBudgetModal callBackBudget={callBackBudget} />
      </div>
    </div>
  );
};
export default Filter;
