import React, { useState, useEffect } from "react";
import Footer from "../../Defaults/Footer";
import Pagination from "../Components/Pagination";
import Filter from "../Services/Filter";
import SerarchListService from "./SerarchListService";
import { reactLocalStorage } from "reactjs-localstorage";
import HttpClient from "../../utils/HttpClient";
const Index = (props) => {
  useEffect(() => {
    getproduct();
  }, []);
  const [reviewlist, setreviewlist] = useState([]);
  const [pro, setpro] = useState([]);

  const [length, setlength] = useState(0);

  const name = props.match.params.p1;
  const type = props.match.params.p2;
  // console.log(name);
  // console.log(type);
  const getproduct = async () => {
    // setLoading(true);
    let sendData = {
      type: type,
      searchname: name,
    };
    console.log("object", sendData);
    let result = await HttpClient.requestData("searchAll", "POST", sendData);
    console.log("singlproresult", result);
    if (result && result.status) {
      setpro(result.data);
      setlength(result.data.length);
      // setTimeout(() => {
      //   setLoading(false);
      // }, 1);
    } else {
    }
  };

  
  return (
    <div>
      <section className="Search-page">
        <div className="container-fluid">
          <div className="row px-5 py-4">
            <div className="col-md-12">
              <h4>
                {length} search results <span>"{name}"</span>
              </h4>
            </div>
          </div>
          <div className="row">
            {/* <div className="col-md-12">
              <Filter />
            </div> */}
          </div>
          <SerarchListService pro={pro} type={type}  />
          <div className="row">
            <div className="col-md-12">
              {/* <Pagination /> */}
            </div>
          </div>
        </div>
      </section>
      <section>
        <Footer />
      </section>
    </div>
  );
};
export default Index;
