import React from "react";
import { Link } from "react-router-dom";

const DescriptionPro = (props) => {
  return (
    <div className="Highlights">
      <div>
        <h5>Descriptions</h5>
        <p>
          {props.description?props.description:""}
        </p>
      </div>
      <Link to="/">
        {" "}
        <div className="text-right">
          <h6>Read More</h6>
        </div>
      </Link>
    </div>
  );
};
export default DescriptionPro;
