import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import { Link } from "react-router-dom";
const Provider = (props) => {
  console.log("top provider props", props.data);
  const settings = {
    dots: false,
    infinite: false,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 2,

    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 2,
          infinite: true,
          dots: true,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          initialSlide: 1,
        },
      },
      {
        breakpoint: 400,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <div className="provider_sec">
      <div className="px-md-0 mx-0">
        <Slider {...settings}>
          {props.data.map((item, index) => {
            return index < 6 ? (
              <div className="card slider2_part px-4 py-4 " key={index}>
                <div className="card-body slider2_body text-center p-0">
                  <img
                    src={
                      item.image
                        ? "https://admin.blitzcode.online/" + item.image
                        : "https://t4.ftcdn.net/jpg/02/07/87/79/360_F_207877921_BtG6ZKAVvtLyc5GWpBNEIlIxsffTtWkv.jpg"
                    }
                    alt="img1"
                    className="m-auto"
                  />

                  <h5 className="card-title my-3 ">
                    {item.firstName + " " + item.lastName}
                  </h5>
                  <p className="card-text">
                    {item.shop_data ? item.shop_data.name : ""}
                  </p>
                  <div className="d-flex justify-content-center mt-2 mb-3 ">
                    <i className="fas fa-star my-1 mr-2"></i>
                    <p>{item.avgRating ? item.avgRating.toFixed(1) : "0"}</p>
                  </div>
                  <Link to={"/service-provider/" + item._id}>
                    <button href="#" className="btn">
                      View More
                    </button>
                  </Link>
                </div>
              </div>
            ) : null;
          })}
        </Slider>
      </div>
    </div>
  );
};
export default Provider;
