import React, { useState } from "react";
import EditBudgetModal from "../Services/EditBudgetModal";
import HttpClient from "../../utils/HttpClient";
import { min } from "moment";

const ProductFilter = (props) => {
  const [budget, setBudget] = useState(false);

  const callBackBudget = (val) => {
    setBudget(val);
  };
  const [price, setprice] = useState("");
  const [delivery, setdelivery] = useState("");
  const [options, setoptions] = useState([
    { label: "Best Selling", value: "bestselling" },
    // { label: "New Arrivals", value: "newarrivals" },
    // { label: "Top Customer Reviews", value: "highlowrev" },
    { label: "Total Sales : Low to High", value: "lowhigh" },
    { label: "Total Sales : High to Low", value: "highlow" },
    { label: "Price : High to Low", value: "highlowsell" },
    { label: "Price : Low to High", value: "lowhighsell" },
    { label: "Top Customer Reviews", value: "highlowrev" },
  ]);

  const handledelivery = async (event) => {
    setdelivery(event.target.value);

    let data = {
      delivery: event.target.value,
    };
    console.log(data);
    let result = await HttpClient.requestData("searchProduct", "POST", data);
    console.log("service", result);
    if (result && result.status) {
      props.filer(result.status, result.data);
    } else {
      props.filer(result.status, result.data);
    }
  };

  const handleprice = async (event) => {
    let min = 0;
    let max = 0;

    if (event.target.value == "under10") {
      min = 0;
      max = 10;
    } else if (event.target.value == "under10-25") {
      min = 10;
      max = 25;
    } else if (event.target.value == "above50") {
      min = 50;
      max = 10000;
    } else {
      min = 25;
      max = 50;
    }
    let data = {
      delivery: delivery,
      min: min,
      max: max,
    };
    console.log(data);
    let result = await HttpClient.requestData("searchProduct", "POST", data);
    console.log("service", result);
    if (result && result.status) {
      props.filer(result.status, result.data);
    } else {
      props.filer(result.status, result.data);
    }
  };

  const sortchoose = async (event) => {
    let data = {
      shortby: event.target.value,
    };

    console.log("send", data);
    let result = await HttpClient.requestData("searchProduct", "POST", data);
    console.log("service", result);
    if (result && result.status) {
      props.filer(result.status, result.data);
    } else {
      props.filer(result.status, result.data);
    }
  };
  return (
    <div className="d-md-flex justify-content-between py-4 filter_part">
      <div className="d-md-flex justify-content-between">
        <div className="mr-3">
          <div className="form-group">
            <select
              className="form-control"
              id="exampleFormControlSelect1"
              onChange={handledelivery}
            >
              <option className="option_sec"> Delivery</option>
              <hr></hr>
              <option value="Free Delivery"> Free Delivery </option>
              <option value="Ready to Dispatch">Ready to dispatch</option>
              <option value="1 business day"> 1 business day </option>
              <option value="1-4 Business days">1-4 Business days</option>
            </select>
          </div>
        </div>
        <div className="mr-3">
          <div className="form-group">
            <select className="form-control" id="exampleFormControlSelect1">
              <option className="option_sec"> Rating </option>
              <hr></hr>
              <option>Hight to low </option>
              <option>low to high </option>
            </select>
          </div>
        </div>
        <div className="mr-3">
          <div className="form-group">
            <select
              className="form-control"
              id="exampleFormControlSelect1"
              onChange={handleprice}
            >
              <option className="option_sec">Select Price</option>
              <hr></hr>
              <option value="under10">$ 0 - $ 50</option>
              <option value="under10-25">$ 51 - $ 100 </option>
              <option value="under25-50"> $ 101 - $ 150</option>
              <option value="above50">more then $ 151</option>
            </select>
          </div>
        </div>
        <div className="mr-3">
          {" "}
          {/* <button
            type="button"
            className="btn budget_btn"
            onClick={() => setBudget(true)}
          >
            Edit Price
          </button> */}
        </div>
      </div>
      <div>
        <div className="">
          <div className="form-group d-flex">
            <h6 className="mr-3 my-2">SortBy:</h6>
            <select
              className="form-control"
              id="exampleFormControlSelect1"
              onChange={sortchoose}
            >
              <option>Products Sorting</option>
              <hr></hr>

              {options.map((item, index) => {
                return <option value={item.value}>{item.label}</option>;
              })}
            </select>
          </div>
        </div>
      </div>
      <div
        className={budget ? "modal fade loginmodal show" : "modal fade"}
        id="exampleModal"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
        style={{ display: budget ? "block" : "none" }}
      >
        <EditBudgetModal callBackBudget={callBackBudget} />
      </div>
    </div>
  );
};
export default ProductFilter;
