import React, { useState, useEffect } from "react";
import Pagination from "../Components/Pagination";
import DescriptionPro from "./DescriptionPro";
import DetailsPro from "./DetailsPro";
import ReviewList from "./ReviewList";
import RelatedProduct from "./RelatedProduct";
import OtherService from "../Components/OtherService";
import Footer from "../../Defaults/Footer";
import { Link } from "react-router-dom";
import { ReactTitle } from "react-meta-tags";
// import Loader from "../../Component/loader";
import HttpClient from "../../utils/HttpClient";
import Loader from "../../Component/loader";

const Index = (props) => {
  useEffect(() => {
    window.scrollTo(0, 0);
    getproduct();
    getreview();
  }, []);
  const loop = [1, 2, 3, 4, 5, 6, 7];
  const [editid, seteditid] = useState(props.match.params.id);
  const [pro, setpro] = useState({});
  const [loading, setLoading] = useState(false);
  const [reviewlist, setreviewlist] = useState([]);
  const [avg, setavg] = useState(0);
  const [reviewcount, setreviewcount] = useState(0);
  const [isDataLoaded, setisDataLoaded] = useState(false)
  const getproduct = async () => {
    setLoading(true);
    let sendData = {};
    console.log("object", sendData);
    let result = await HttpClient.requestData(
      "viewproduct/" + editid,
      "GET",
      sendData
    );
    console.log("singlproresult", result);
    if (result && result.status) {
      setpro(result.data[0]);
      setisDataLoaded(true);
      setTimeout(() => {
        setLoading(false);
      }, 1);
    } else {
    }
  };

  const getreview = async () => {
    // setLoading(true);
    let sendData = {};
    console.log("object", sendData);
    let result = await HttpClient.requestData(
      "productreview/" + editid,
      "GET",
      sendData
    );
    console.log("review", result);
    if (result && result.status) {
      // setTimeout(() => {
      //   setreviewlist(result.data);
      // }, 1);
      setreviewlist(result.data);
      setreviewcount(result.data.length);
      let sum = 0;
      let avg = 0;
      // console.log("reviewlist", reviewlist);
      result.data.map((itemm, index) => {
        return (sum = Number(sum) + Number(itemm.rating));
      });
      // console.log("sum",sum);
      avg = Number(sum) / Number(result.data.length);

      setavg(avg);
    } else {
    }
  };

  return (
    <div>
      <div className="wrapper">
        <ReactTitle title="Astrophy-Product Details" />
      </div>

      {loading ? (
        <Loader />
      ) : (
        <>
          <div className="container-fluid">
            <div className="row p-4">
              <div className="col-md-12 back_to">
                <Link to="/our-products">
                  <p>
                    {" "}
                    <i className="fas fa-chevron-left mr-2"></i>Back to Products
                  </p>
                </Link>
              </div>
            </div>
          </div>
          <section>
            <div className="container-fluid">
              {isDataLoaded ? (
                 <DetailsPro
                 details={pro}
                 avg={pro.avgRating}
                 reviewcount={reviewcount}
               />
              ) : null}
             
            </div>
          </section>
          <section>
            <div className="container-fluid">
              <div className="row p-4">
                <div className="col-md-12">
                  {/* {reviewcount} */}
                  <DescriptionPro description={pro.description} />
                </div>
              </div>
            </div>
          </section>
          <section>
            <div className="container-fluid">
              <div className="row p-4">
                <div className="col-md-12">
                  <div className="d-flex border-bottom">
                    <div className="select active mr-5">Product Reviews</div>

                    <div className="form-group d-flex ml-auto review_sort">
                      <h6 className="mr-3 my-2">SortBy:</h6>
                      <select
                        className="form-control"
                        id="exampleFormControlSelect1"
                      >
                        <option>Newly Added</option>
                        <option>2</option>
                        <option>3</option>
                        <option>4</option>
                        <option>5</option>
                      </select>
                    </div>
                  </div>
                  <ReviewList data={loop} reviewlist={reviewlist} />
                </div>
              </div>
            </div>
          </section>
          <section className="container">
            <div className="row">
              <div className="col-md-12 ">
                {/* <Pagination /> */}
              </div>
            </div>
          </section>
          <section className="my-5">
            <div className="container-fluid ">
              <div className="row p-4">
                <div className="col-md-12 Related">
                  <h4>Related Products</h4>
                </div>
              </div>
            </div>
            <RelatedProduct />
          </section>
          <section>
            <div className="container-fluid">
              <div className="row">
                <div className="col-md-12 my-2">
                  <div className="service_head">
                    <h6>Reading</h6>
                    <h3 className="my-4">Other Services</h3>
                  </div>
                </div>
              </div>
              <OtherService />
            </div>
          </section>
        </>
      )}
      <section>
        <Footer />
      </section>
    </div>
  );
};
export default Index;
