import React, { useEffect, useState } from "react";
import { ReactTitle } from "react-meta-tags";
import { Link } from "react-router-dom";
import cardImg1 from "../../Assets/Images/detailspro_pic.png";
import cardImg2 from "../../Assets/Images/il_794xN 1 (1).png";
import { reactLocalStorage } from "reactjs-localstorage";
import HttpClient from "../../utils/HttpClient";
import Footer from "../../Defaults/Footer";
import ServiceCartList from "./ServiceCartList";
import Loader from "../../Component/loader";
import moment from "moment";
const Cart = () => {
  useEffect(() => {
    window.scrollTo(0, 0);
    getcart();
  }, []);
  const [loading, setLoading] = useState(false);
  const [coupon, setcoupon] = useState("");
  const [discount, setdiscount] = useState(0);
  const userData = reactLocalStorage.getObject("userData");
  const [sum, setsum] = useState(0);
  const [loop, setloop] = useState([]);
  const [count, setcount] = useState(0);

  const [valid, setvalid] = useState(false);

  const [invalid, setinvalid] = useState(false);

  const [coupondata, setcoupondata] = useState([]);

  const setarr = (index, value) => {
    let arr = loop;
    arr[index].notes = value;
    setloop(arr);
    // console.log("loop", arr);
  };
  const getcart = async () => {
    setLoading(true);
    let data = {};
    // console.log("data", data);
    let result = await HttpClient.requestData(
      "servicecart/" + userData._id,
      "GET",
      data
    );
    console.log("result", result);
    if (result && result.status) {
      setloop(result.data);
      setcount(result.data.length);
      let sum = 0;
      result.data.forEach((element) => {
        sum = sum + element.price;
      });
      // console.log("sum", sum);
      setsum(sum);
      setTimeout(() => {
        setLoading(false);
      }, 1);
    } else {
    }
  };

  const success = (val) => {
    console.log("object", val);
    if (val) {
      getcart();
    }
  };

 

  

  const checkout = async () => {
   
    window.location.href = "/checkoutservice";
  };

  return (
    <div>
      <div className="wrapper">
        <ReactTitle title="Astrophy - Add to Cart" />
      </div>
      {loading ? (
        <Loader />
      ) : (
        <>
          {" "}
          <section>
            <div className="container-fluid">
              <div className="row px-4 pt-4">
                <div className="col-md-12 cart_head">
                  <h3>{count} Items in your Basket</h3>
                </div>
              </div>
              <div className="row px-4">
                <ServiceCartList
                  data={loop}
                  success={success}
                  
                  setarr={setarr}
                />
                <div className="col-md-4 ">
                  <div className="cart_box_right p-4 my-3">
                    <div className="border-bottom my-4">
                      <h4>Your Cart</h4>
                      <div className="d-flex justify-content-between">
                        <p>Item(s) Total</p>
                        <p> {sum}</p>
                      </div>
                    </div>

                    <div>
                      <div className="d-flex justify-content-between">
                        <h5>Total Amount</h5>
                        <h5> {sum}</h5>
                      </div>{" "}
                      <div className="text-center my-4">
                        <button className="btn proceed_btn" onClick={checkout}>
                          Proceed to Checkout
                        </button>
                      </div>
                      <p className="text-center">
                        Local taxes included (where applicable) * Additional
                        duties and taxes{" "}
                      </p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </section>
        </>
      )}

      <section>
        <Footer />
      </section>
    </div>
  );
};
export default Cart;
