import React from "react";
import { Link } from "react-router-dom";
import Blog_ban from "../../Assets/Images/blog.png";
const LatestBlog = () => {
  const loop = [1, 2, 3, 4, 5];
  return (
    <div className="row">
      {loop.map((item, index) => {
        return (
          <div className="col-md-6 col-lg-4">
            <Link to="/blog-details" className="d-block">
              <div className="latest_box my-4" key={index}>
                <div className="row flex-column">
                  <div className="col-md-12">
                    <div>
                      <img src={Blog_ban} alt="img" />
                    </div>
                  </div>
                  <div className="col-md-12">
                    <div className="px-3 py-2">
                      <h6>April 25, 2021</h6>
                      <h4>5 ASTRONOMY BOOKS FOR BEGINNERS</h4>
                      <p>
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                        A, volutpat nibh cursus. Lorem ipsum dolor sit amet,
                        consectetur adipiscing elit. A, volutpat nibh cursus{" "}
                      </p>
                      <span>Read More</span>
                    </div>
                  </div>
                </div>
              </div>
            </Link>
          </div>
        );
      })}
    </div>
  );
};
export default LatestBlog;
