import React, { useState,useEffect } from "react";
import Chart from "react-apexcharts";
import Select from "react-select";
import HttpClient from "../../utils/HttpClient";
import { reactLocalStorage } from "reactjs-localstorage";

const optionss = [
  { value: "todaysTotal", label: "Today" },
  { value: "yestardayTotal", label: "Yesterday" },
  { value: "sevendaysTotal", label: "Last 7 Days" },
  { value: "monthTotal", label: "Last 30 Days" },
  { value: "yearTotal", label: "Last Year" },
];

export default function Graph() {
  const [graphData, setgraphData] = useState();
 
  const [graphValue, setgraphValue] = useState("");
  
  const handleChange =(selectedOption) => {
    console.log("selectedOption",selectedOption);
    setgraphValue(selectedOption);
  };

  useEffect(() => {
    fetchGraph();
  }, []);

  const fetchGraph = async () => {
    let userData = reactLocalStorage.getObject("userData")
    let sendData = {
      id: userData._id
    }
    let result = await HttpClient.requestData("getGraphcomission", "POST", sendData);
    // console.log("graph data", result);
    if (result && result.status) {
     setgraphData(result);
   
    
    } else {
    }
  };
  
  const values = graphData ? [graphData.todaysTotal,graphData.yestardayTotal,graphData.sevendaysTotal,graphData.monthTotal,graphData.yearTotal] : "";
  console.log("values", values);

  const options = {
    xaxis: {
      categories: ["Today", "Yesterday", "Last 7 Days", "Last 30 Days", "Last Year"],
    },
  };
  
  const series = [
    {
      name: "series-1",
      // data: [30, 40, 45, 50, 49, 60, 70, 91],
      data: [...values],
    },
  ];
  return (
    <div>
      {/* <div className="row">
        <div className="col-6">
          <div className="form-group">
          <Select
            value={graphValue}
            onChange={handleChange}
            options={optionss}
          />
          </div>
        </div>
      </div> */}
      <div className="mixed-chart">
        <Chart options={options} series={series} type="bar" width="500" />
      </div>
    </div>
  );
}
