import React, { useState } from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Provider_name from "../../Assets/Images/provider_name.png";
import Favourite from "../../Assets/Images/favourite.png";
import { Link } from "react-router-dom";
import ServiceModal from "../../Seller/MyServices/ServiceModal";
import ServiceDlt from "../../Seller/MyServices/ServiceDlt";
import EmptyService from "../EmptyProduct/EmptyService";

const ServiceList = (props) => {
  console.log("props", props.data);
  const [servicemodal, setServicemodal] = useState(false);
  const [servicedlt, setServicedlt] = useState(false);
  const [activemodal, setactivemodal] = useState({});
  const [delid, setdelid] = useState(false);
  const callBackService = (val) => {
    setServicemodal(val);
    setactivemodal({});
  };
  const callBackServiceDlt = (val, val1) => {
    setServicedlt(val);
    if (val1 != "cancel") {
      window.location.href = "/seller-service"
    }
  };

  const success = (val) => {
    console.log("object", val);
    if (val) {
      props.refresh(true);
    }
  };

  // const handleSubmit =(itm) =>{
  //   setactivemodal(itm);
  //   console.log("activemodal",activemodal);
  //   setTimeout(() => {
  //     setServicemodal(true);
  //   }, 100);
  // };
  // console.log("activeModal", activemodal);
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
  };
  const loop = [1, 2, 3, 4];
  return (
    <div>
      <div className="row px-4">
        {props.data.length > 0 ? (
          props.data.map((item, index) => {
            return (
              <>
                {" "}
                <div className="col-lg-3 col-md-6 my-3" key={index}>
                  <div className="card service_list">
                    <Slider {...settings}>
                      {item.image ? (
                        item.image.map((item2, index2) => {
                          return (
                            <div key={index2}>
                              {" "}
                              {props.type === "edit" ? (
                                <img
                                  src={"https://admin.blitzcode.online/" + item2}
                                  alt="img"
                                  className="relative service_list_img"
                                />
                              ) : (
                                <Link to={"/service-details/" + item._id}>
                                  <img
                                    src={
                                      "https://admin.blitzcode.online/" + item2
                                    }
                                    alt="img"
                                    className="relative service_list_img"
                                  />
                                </Link>
                              )}
                              <img
                                src={Favourite}
                                alt="img"
                                className="absolute m-2"
                              />
                            </div>
                          );
                        })) : ""}
                    </Slider>

                    <div className="card-body service_list_text">
                      <div className="d-flex">
                        <i className="fas fa-star my-1 mr-2"></i>

                        <h6>{item.avgRating ? item.avgRating : 0}/5</h6>
                        {props.type === "edit" ? (
                          <div className="ml-auto">
                            <button
                              type="button"
                              className="btn "
                              // onClick={() => setServicemodal(true)}
                              onClick={() => {
                                setactivemodal(item);
                                setTimeout(() => {
                                  setServicemodal(true);
                                }, 100);
                                // setServicemodal(true);
                              }}
                            >
                              <i className="fas fa-pencil-alt text-dark"></i>
                            </button>
                            <button
                              type="button"
                              className="btn "
                              onClick={() => {
                                setServicedlt(true)
                                setdelid(item._id)
                              }
                              }
                            >
                              <i className="fas fa-trash-alt text-danger"></i>
                            </button>
                          </div>
                        ) : (
                          ""
                        )}
                      </div>
                      <Link to={"/service-details/" + item._id}>
                        <p className="card-text">{item.name}</p>
                      </Link>
                      <h5 className="my-3">$ {item.price}</h5>
                      <div className="d-flex mt-3">
                        <img
                          src={Provider_name}
                          alt="img"
                          className="mr-2"
                          style={{
                            width: "100%",
                            height: "25px",
                            maxWidth: "25px",
                          }}
                        />
                        <h4>
                          {item.shop_details ? item.shop_details[0].name : null}
                        </h4>
                      </div>
                    </div>
                  </div>
                </div>
                <div
                  className={
                    servicemodal ? "modal fade loginmodal show" : "modal fade"
                  }
                  id="exampleModal1"
                  tabIndex="-1"
                  aria-labelledby="exampleModalLabel"
                  aria-hidden="true"
                  style={{ display: servicemodal ? "block" : "none" }}
                >
                  {Object.keys(activemodal).length == 0 ? null : (
                    <ServiceModal
                      callBackService={callBackService}
                      item={activemodal}
                      success={success}
                    />
                  )}
                </div>
                <div
                  className={
                    servicedlt ? "modal fade loginmodal show" : "modal fade"
                  }
                  id="exampleModal2"
                  tabIndex="-1"
                  aria-labelledby="exampleModalLabel"
                  aria-hidden="true"
                  style={{ display: servicedlt ? "block" : "none" }}
                >
                  <ServiceDlt callBackServiceDlt={callBackServiceDlt}
                    delid={delid}
                  />
                </div>
              </>
            );
          })
        ) : (
          <EmptyService />
        )}
      </div>
    </div>
  );
};
export default ServiceList;
