import React, { useEffect, useState } from "react";
import Blog_ban from "../../Assets/Images/blog_ban_img.png";
import Footer from "../../Defaults/Footer";
import Pagination from "../Components/Pagination";
import LatestBlog from "./LatestBlog";
import MorePopular from "./MorePopular";
import SubsBox from "./SubsBox";
import { ReactTitle } from "react-meta-tags";
const Index = () => {
  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  const [tab, setTab] = useState("all");
  return (
    <div>
      <div className="wrapper">
        <ReactTitle title="Astrophy-Blog" />
      </div>
      <section>
        <div className="container-fluid">
          <div className="row p-4">
            <div className="col-md-12 Blog_head">
              <h3>Blog</h3>
              <div className="d-md-flex  my-4">
                <div>
                  <button
                    className={
                      tab === "all"
                        ? "provide_tab active btn mr-2 my-md-0 my-2"
                        : "provide_tab btn mr-2 my-md-0 my-2"
                    }
                    onClick={() => {
                      setTab("all");
                    }}
                  >
                    All (42)
                  </button>
                  <button
                    className={
                      tab === "psychic"
                        ? "provide_tab active btn mr-2 my-md-0 my-2"
                        : "provide_tab btn mr-2 my-md-0 my-2"
                    }
                    onClick={() => {
                      setTab("psychic");
                    }}
                  >
                    Category 1 (39)
                  </button>
                  <button
                    className={
                      tab === "reiki"
                        ? "provide_tab active btn mr-2 my-md-0 my-2"
                        : "provide_tab btn mr-2 my-md-0 my-2"
                    }
                    onClick={() => {
                      setTab("reiki");
                    }}
                  >
                    Category 2 (03)
                  </button>
                  <button
                    className={
                      tab === "preg"
                        ? "provide_tab active btn mr-2 my-md-0 my-2"
                        : "provide_tab btn mr-2 my-md-0 my-2"
                    }
                    onClick={() => {
                      setTab("preg");
                    }}
                  >
                    Category 3 (06)
                  </button>
                </div>
                <div className="form-group d-flex ml-auto review_sort">
                  <h6 className="mr-3 my-2">SortBy:</h6>
                  <select
                    className="form-control"
                    id="exampleFormControlSelect1"
                  >
                    <option>Newly Added</option>
                    <option>Most Popular</option>
                  </select>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section>
        <div className="container-fluid">
          <div className="row px-3">
            <div className="col-lg-9">
              <div className="blog_ban_img">
                <img
                  src={Blog_ban}
                  alt="img"
                  // style={{ width: "100%", height: "100%", height: "510px" }}
                />
                <div className="blog_ban_overlay d-flex justify-content-center align-items-end">
                  <h6>5 ASTRONOMY BOOKS FOR BEGINNERS</h6>
                </div>
              </div>
            </div>
            <div className="col-lg-3 my-lg-0 my-4 ">
              <SubsBox />
            </div>
          </div>
        </div>
      </section>
      <section>
        <div className="container-fluid">
          <div className="row p-4">
            <div className="col-md-9 Latest_blog">
              <h6>Latest Blogs</h6>
              <div className="container">
                <LatestBlog />
              </div>
            </div>
            <div className="col-md-3 Latest_blog">
              <h6>More Popular</h6>
              <MorePopular />
            </div>
          </div>
        </div>
      </section>
      <section className="container">
        <div className="row">
          <div className="col-md-12 ">{/* <Pagination /> */}</div>
        </div>
      </section>
      <section>
        <Footer />
      </section>
    </div>
  );
};
export default Index;
