import React, { useState } from "react";
import Provider_name from "../../Assets/Images/provider_name.png";
import { reactLocalStorage } from "reactjs-localstorage";
import HttpClient from "../../utils/HttpClient";
import EmptyCart from "../EmptyCart/EmptyCart";
const CartList = (props) => {
  
  const remove = async (item) => {
    let data = {};
    let result = await HttpClient.requestData(
      "servicecart/" + item._id,
      "DELETE",
      data
    );
    console.log("result", result);
    if (result && result.status) {
      props.success(result.status);
    } else {
    }
  };
  const userData = reactLocalStorage.getObject("userData");
  
  return (
    <div className="col-md-8">
      {props.data.length > 0 ? (
        props.data.map((item, index) => {
          return (
            <div className="col-md-12 card cart_box my-4" key={index}>
              <div className="row card-body px-4">
                <div className="col-md-2">
                  <img
                    src={"https://admin.blitzcode.online/" + item.image}
                    alt="img"
                    style={{ width: "100%" }}
                  />
                </div>
                <div className="col-md-8">
                  <h5>{item.servicename}</h5>
                  <p>
                    Personalisation : lorem ipsum is dummy text used for
                    printing
                  </p>
                  {item.type ? (
                    <div className="mt-4">
                      <div className="d-flex ">
                        <div>
                          <img src={Provider_name} alt="img" />
                        </div>
                        <div>
                          <h4>T Boutique</h4>
                          <p>Owner of TarotBoutique555</p>
                        </div>
                      </div>
                    </div>
                  ) : (
                    ""
                  )}
                  {/* <div className="form-group form-check ">
                    <input
                      type="checkbox"
                      className="form-check-input"
                      id="exampleCheck1"
                    />
                    <label className="form-check-label" htmlFor="exampleCheck1">
                      This order is a gift
                    </label>
                    <p>Prices will not be shown on packing slip</p>
                  </div> */}
                </div>
                <div className="col-md-2">
                  <div className="d-flex justify-content-around">
                    {" "}
                    {/* <button
                      type="button"
                      className="btn side_btn"
                      onClick={() => wishlist(item)}
                    >
                      <p>Save For Later</p>
                    </button> */}
                    <button
                      type="button"
                      className="btn side_btn"
                      onClick={() => remove(item)}
                    >
                      <p>Remove</p>
                    </button>
                  </div>
                  <h6> {item.price }</h6>
                  
                </div>
              </div>
              <div className="row card-body px-4 ">
                <div className="col-md-2"></div>
                <div className="col-md-10">
                  <div className="form-group ">
                    <label htmlFor="exampleFormControlTextarea1">
                      Add additional notes
                    </label>
                    <textarea
                      className="form-control my-2"
                      id="exampleFormControlTextarea1"
                      rows={3}
                      defaultValue={""}
                      style={{ marginTop: "-10px" }}
                      // defaultValue={item.description}
                      onChange={(item) => {
                        // console.log(item.target.value);
                        props.data[index].notes = item.target.value;
                        props.setarr(index, item.target.value);
                      }}
                    />
                  </div>
                  <div className="text-right">
                    <ul>
                      <li>
                        {" "}
                        <i className="fas fa-circle  mr-2"></i>Ready to dispatch
                        in 13 business days from United States
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          );
        })
      ) : (
        <EmptyCart />
      )}
    </div>
  );
};
export default CartList;
