import React, { useEffect, useState } from "react";
import Spell from "../Components/Spell";
import Filter from "../Services/Filter";
import ProductList from "./ProductList";
import Product1_img from "../../Assets/Images/thought-catalog-o0Qqw21-0NI-unsplash 1.png";
import Product2_img from "../../Assets/Images/thought-catalog-o0Qqw21-0NI-unsplash 1 (1).png";
import Product3_img from "../../Assets/Images/thought-catalog-o0Qqw21-0NI-unsplash 1 (2).png";
import Product4_img from "../../Assets/Images/thought-catalog-o0Qqw21-0NI-unsplash 1 (3).png";
import Product5_img from "../../Assets/Images/thought-catalog-o0Qqw21-0NI-unsplash 1 (4).png";
import Product6_img from "../../Assets/Images/thought-catalog-o0Qqw21-0NI-unsplash 1 (5).png";
import Footer from "../../Defaults/Footer";
import { ReactTitle } from "react-meta-tags";
import HttpClient from "../../utils/HttpClient";
import Loader from "../../Component/loader";
import ProductFilter from "./ProductFilter";
import Pagination from "../Components/Pagination";
import { reactLocalStorage } from "reactjs-localstorage";
const Index = () => {
  useEffect(() => {
    window.scrollTo(0, 0);
    getproduct();
  }, []);

  const [productpaginator, setproductpaginator] = useState({});
  const [userdata, setuserdata] = useState(
    reactLocalStorage.getObject("userData")
  );
  const getproduct = async (next) => {
    setLoading(true);

    let datass = "/k";
    if (Object.keys("userData").length > 0) {
      if (typeof userdata._id !== "undefined") {
        datass = "/" + userdata._id;
      } else {
        datass = "/91a953b1effa7e26d08fff8a";
      }
    }
    let sendData = next ? next : "1";
    console.log("data......", datass);
    let result = await HttpClient.requestData(
      "listProducts/" + sendData + datass,
      "GET",
      {}
    );
    console.log("result", result);
    if (result && result.status) {
      setitem(result.data.itemsList);
      setproductpaginator(result.data.paginator);
      setTimeout(() => {
        setLoading(false);
      }, 1);
    } else {
    }
  };

  const nextpage = (val) => {
    getproduct(val);
  };
  const filer = (val, data) => {
    if (val) {
      setitem(data);
    } else {
      setitem(data);
    }
  };

  const [item, setitem] = useState([]);
  const [loading, setLoading] = useState(false);
  return (
    <div>
      <div className="wrapper">
        <ReactTitle title="Astrophy-Our Products" />
      </div>
      {loading ? (
        <Loader />
      ) : (
        <>
          <section className="Spell">
            <Spell />
          </section>
          <div className="container my-4">
            <div className="row">
              <div className="col-md-12 text-center Product_list_head">
                <h3>Our Products</h3>
              </div>
            </div>
          </div>
          <section className="Services">
            <div
              className="container Product_list"
              style={{ maxWidth: "1200px" }}
            >
              <ProductFilter page="products" filer={filer} />
            </div>
          </section>

          <section className="mb-3">
            <div
              className="container Product_list"
              style={{ maxWidth: "1200px" }}
            >
              <ProductList data={item} />
              {/* <div className="text-center">
                <button href="#" className="btn my-4 product_list_btn ">
                  View More
                </button>
              </div> */}
            </div>
          </section>
        </>
      )}

      <section className="container">
        <div className="row">
          <div className="col-md-12 ">
            <Pagination
              productpaginator={productpaginator}
              nextpage={nextpage}
            />
          </div>
        </div>
      </section>
      <section>
        <Footer />
      </section>
    </div>
  );
};
export default Index;
