import React, { useState, useEffect } from "react";
import order1 from "../../Assets/Images/il_794xN 4.png";
import moment from "moment";
import Provider_name from "../../Assets/Images/provider_name.png";
import ReactStars from "react-rating-stars-component";
import { reactLocalStorage } from "reactjs-localstorage";
import OrderDLT from "./OrderDLT";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import HttpClient from "../../utils/HttpClient";
const ServiceOrderDetails = (props) => {
  console.log("props", props.orderdetails);
  useEffect(() => {
    // console.log("useeffect running");
    setrating(0);
    setreview("");
  }, [props.orderdetails]);
  const [orderDlt, setOrderDlt] = useState(false);
  const [rating, setrating] = useState(0);
  const [tips, settips] = useState("");
  const [review, setreview] = useState("");
  const userData = reactLocalStorage.getObject("userData");
  const ratingChanged = async (newRating) => {
    console.log(newRating);
    setrating(newRating);
  };
  const callBackOrder = (val) => {
    setOrderDlt(val);
  };
  const submit = async (item) => {
    let data = {
      user_id: userData._id,
      service_id: item.serv_id,
      rating: rating,
      comment: review,
      order_id: item.order_id,
    };
    console.log(data);
    let result = await HttpClient.requestData("servicereview", "POST", data);
    console.log("result1", result);
    if (result && result.status) {
      setrating(0);
      setreview("");
      toast.success(result.message, {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    } else {
      setrating(0);
      setreview("");
      toast.error(result.message, {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  };

  const submitTips = async () => {
    if (tips == "") {
      toast.error("Please enter tips", {
        position: "bottom-right",
        autoClose: 2000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    } else {
      let data = {
        id: props.orderdetails._id,
        tip: tips,
      };
      let result = await HttpClient.requestData("tips", "POST", data);
      console.log("paid tip", result);
      if (result && result.status) {
        toast.success(result.message, {
          position: "bottom-right",
          autoClose: 1000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
        settips("");
      } else {
        toast.error(result.message, {
          position: "bottom-right",
          autoClose: 1000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      }
    }
  };

  const success = (val) => {
    if (val) {
      props.refresh(val);
    }
  };
  return (
    <div>
      <ToastContainer />
      {props.orderdetails.servicecart_data
        ? props.orderdetails.servicecart_data.map((item, index) => {
            return (
              <div className="row">
                <div className="col-md-12">
                  <div className="d-md-flex  my-3 justify-content-around border-bottom p-3">
                    <div className="order_pic mx-3">
                      <img
                        src={"https://admin.blitzcode.online/" + item.image}
                        alt="img"
                      />
                    </div>
                    <div>
                      <h4>{item.servicename}</h4>
                      <p>
                        Personalisation : lorem ipsum is dummy text used for
                        printing
                      </p>

                      <div>
                        {/* <h5>Review</h5> */}

                        <div className="form-group">
                          <textarea
                            className="form-control"
                            id="exampleFormControlTextarea1"
                            rows={3}
                            // defaultValue={"Write Review"}
                            value={review}
                            onChange={(val) => setreview(val.target.value)}
                          />
                        </div>
                        <ReactStars
                          count={5}
                          value={rating}
                          onChange={ratingChanged}
                          size={24}
                          isHalf={true}
                          emptyIcon={<i className="far fa-star"></i>}
                          halfIcon={<i className="fa fa-star-half-alt"></i>}
                          fullIcon={<i className="fa fa-star"></i>}
                          activeColor="#ffd700"
                        />
                        <button
                          type="button "
                          className="btn submit_btn mt-2"
                          onClick={() => submit(item)}
                        >
                          Submit
                        </button>
                        {/* <RatingChanged /> */}

                        {/* <h6>
                          Obsessed! I began going to her this week and I already
                          got 4 readings. She is super accurate. I think I
                          finally found the right one on here! Will continue to
                          purchase more gigs
                        </h6> */}
                      </div>
                    </div>
                    <div className="mx-3 right">
                      <h6>{item.price}</h6>
                      {/* <p>Quantity : {item.qty}</p> */}
                    </div>
                  </div>
                </div>
              </div>
            );
          })
        : null}

      <div className="row ">
        <div className="col-md-12">
          <div className="order_status p-3 border-bottom">
            <h3>Order Status</h3>
            <div className="d-flex">
              <i className="fas fa-check-circle mr-2 my-3"></i>
              <div>
                <h6>Order Placed</h6>
                <p>
                  Your order may placed on{" "}
                  {moment(props.orderdetails.booking_date).format(
                    "MMM,Do,h:mm a"
                  )}
                </p>
              </div>
            </div>
          </div>
        </div>
        <div className="col-md-12">
          <div className="shipping p-3 border-bottom">
            <h3>Shipping Address</h3>
            <h6>
              {props.orderdetails.address1 +
                "," +
                props.orderdetails.state +
                "," +
                props.orderdetails.country +
                "," +
                props.orderdetails.zip}
            </h6>
            {/* <p>Phone Number : +1 123 456 7895</p> */}
          </div>
        </div>
        <div className="col-md-12">
          <div className="shipping p-3 border-bottom">
            <h3>Tip</h3>
            <div className="form-group">
              <div className="d-flex tip_box justify-content-between">
                {" "}
                <input
                  type="text"
                  className=" form-control border-0"
                  id="exampleInput"
                  placeholder="Enter Tip Amount"
                  onChange={(val) => {
                    if (val.target.value.match("^[0-9]*$") != null) {
                      settips(val.target.value);
                    }
                  }}
                  value={tips}
                />
                <div className="mb-3">
                  <button className="btn pay_btn " onClick={submitTips}>
                    Pay
                  </button>
                </div>
              </div>
            </div><br />

            <div className="form-group">
              <div className="d-flex tip_box justify-content-between">
                {" "}
                <input
                  type="text"
                  className=" form-control border-0"
                  id="exampleInput"
                  value={props.orderdetails ? props.orderdetails.tip : ""}
                />
                <div className="mb-4">
                  <button className="btn pay_btn ">
                    Paid
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="col-md-12">
          <div className="p-3 invoice">
            <h3>Invoice</h3>
            <div className="d-flex justify-content-between">
              <p>Item(s) Total</p>
              <p> {props.orderdetails.subtotal}</p>
            </div>{" "}
            <div className="d-flex justify-content-between border-bottom">
              <p>Coupon Code Applied</p>
              <p>
                -  {props.orderdetails.subtotal - props.orderdetails.total}
              </p>
            </div>
          </div>
        </div>
        <div className="col-md-12">
          <div className="p-3 invoice">
            {/* <div className="d-flex justify-content-between">
              <p>Delivery Fees</p>
              <p> 20.00</p>
            </div>{" "}
            <div className="d-flex justify-content-between border-bottom">
              <p>Coupon Code Applied</p>
              <p>-  20.00</p>
            </div> */}
          </div>
        </div>
        <div className="col-md-12">
          <div className="p-3 total">
            <div className="d-flex justify-content-between ">
              <p>Total Amount</p>
              <p> {props.orderdetails.total}</p>
            </div>
          </div>
        </div>
        <div className="col-md-12 ">
          <div className="d-flex justify-content-between px-3">
            <p>Disclaimer:</p>
            <p>It includes all the taxes</p>
          </div>
        </div>
        {props.orderdetails.status == "cancel" ? (
          <div className="col-md-12 text-right ">
            <div className="border-top py-2">
              {" "}
              <h3 style={{ color: "red" }}>Order Cancelled</h3>
            </div>
          </div>
        ) : (
          <div className="col-md-12 text-right ">
            <div className="border-top py-2">
              {" "}
              <button
                type="button"
                className="btn cancel_btn"
                onClick={() => setOrderDlt(true)}
              >
                Cancel Order
              </button>
            </div>
          </div>
        )}
      </div>
      <div
        className={orderDlt ? "modal fade loginmodal show" : "modal fade"}
        id="exampleModal"
        tabIndex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
        style={{ display: orderDlt ? "block" : "none" }}
      >
        <OrderDLT
          callBackOrder={callBackOrder}
          orderid={props.orderdetails._id}
          success={success}
        />
      </div>
    </div>
  );
};
export default ServiceOrderDetails;
