import React from "react";

import { Link } from "react-router-dom";

const Product = (props) => {
  return (
    <div className="row Product_sec px-4">
      {props.data.map((item, index) => {
        return (
          <div className="col-md-4 mb-5" key={index}>
            <div className="card product_box ">
              <img src={item.img} className="card-img-top" alt="img" />
              <div className="card-body product_list_text pt-2">
                <Link to="/our-products">
                  <h6>{item.name}</h6>
                </Link>
                <p className="card-text py-3 text-dark"> 00.00</p>
                <div className="d-flex justify-content-between align-items-center">
                  <div className="rating">
                    {" "}
                    <i className="fas fa-star my-1 mr-2"></i>
                    4.6/5
                  </div>
                  <Link to="/add-cart">
                    {" "}
                    <div className="cart ">+ Add to Cart</div>
                  </Link>
                </div>
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
};
export default Product;
