import React, { useState, useEffect } from "react";
import Footer from "../../Defaults/Footer";
import Loader from "../../Component/loader";
import { reactLocalStorage } from "reactjs-localstorage";
import HttpClient from "../../utils/HttpClient";
import swal from "sweetalert";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useHistory } from "react-router-dom";
// import Warning from "../../Component/Warning";
import StripeCheckout from "react-stripe-checkout";
import PayPal from "./PayPal";

var id = "";
const CheckoutService = (props) => {
  const [finishStatus, setfinishStatus] = useState(false);
  // const {history} = useRouter();

  const history = useHistory();
  useEffect(() => {
    getcart();
  }, []);

  useEffect(() => {
    window.addEventListener("popstate", onBackButtonEvent);
    return () => { };
  }, []);

  const onBackButtonEvent = async (e) => {
    e.preventDefault();
    console.log("itemid", id);
    deletecart(id);
    alert("Do you want to go back?");
  };
  const [loading, setLoading] = useState(false);
  const userData = reactLocalStorage.getObject("userData");

  const coupondetails = reactLocalStorage.getObject("coupondetails");
  const discount = reactLocalStorage.getObject("discount");

  // console.log("discount",discount)
  const [sum, setsum] = useState(0);
  const [loop, setloop] = useState([]);
  const [count, setcount] = useState(0);
  const [fname, setfname] = useState("");
  const [lname, setlname] = useState("");
  const [email, setemail] = useState("");
  const [address, setaddress] = useState("");
  const [address2, setaddress2] = useState("");
  const [country, setcountry] = useState("");
  const [state, setstate] = useState("");
  const [zip, setzip] = useState("");
  const [payment, setpayment] = useState("");

  const [itemid, setitemid] = useState("");

  const [blank, setblank] = useState(false);

  const [blanklast, setblanklast] = useState(false);

  const [blankemail, setblankemail] = useState(false);

  const [blankaddress, setblankaddress] = useState(false);

  const [blankcountry, setblankcountry] = useState(false);

  const [blankstate, setblankstate] = useState(false);

  const [blankzip, setblankzip] = useState(false);

  const onToken = (token, addresses) => {
    console.log("token", token);
    console.log("addresses", addresses);

    stripeapicall(token.id);
  };

  const senddetails = (val, details) => {
    if (val) {
      console.log("details", details);
      // NotificationManager.success('Success message', 'Payment is successful');
      order(details.id);
    }
  };

  const stripeapicall = async (token) => {
    console.log(token);
    const card = {
      amount: sum * 100,
      currency: "INR",
      source: token,
      description: "Astrophy Service Payment",
    };

    let result = await fetch("https://api.stripe.com/v1/charges", {
      headers: {
        // Use the correct MIME type for your server
        Accept: "application/json",
        // Use the correct Content Type to send data to Stripe
        "Content-Type": "application/x-www-form-urlencoded",
        // Use the Stripe publishable key as Bearer
        Authorization: `Bearer sk_test_51JavJLSB5KAxkRypR3jypiPJk4I9nQ7L9uGMqDwQSPXCEhwFhYRMGVLpp5UvQTXFYJ6GQ4MqXfAW1cVFxbqEvpR900Bc3ynSKr`,
      },
      // Use a proper HTTP method
      method: "post",
      // Format the credit card data to a string of key-value pairs
      // divided by &
      body: Object.keys(card)
        .map((key) => key + "=" + card[key])
        .join("&"),
    }).then((response) => response.json());

    console.log("resultt", result);

    order(result.balance_transaction);
  };

  const getcart = async () => {
    setLoading(true);
    let data = {};
    // console.log("data", data);
    let result = await HttpClient.requestData(
      "servicecart/" + userData._id,
      "GET",
      data
    );
    console.log("result", result);
    if (result && result.status) {
      setitemid(result.data[0]._id);
      id = result.data[0]._id;
      setloop(result.data);
      setcount(result.data.length);
      let sum = 0;
      result.data.forEach((element) => {
        sum = sum + element.price;
      });
      // console.log("sum", sum);
      setsum(sum);
      setTimeout(() => {
        setLoading(false);
      }, 1);
    } else {
    }
  };

  const handlepayment = (e) => {
    setpayment(e.target.value);
  };

  const order = async (transaction_id) => {
    // e.preventDefault();
    // if (
    //   fname == "" ||
    //   lname == "" ||
    //   address == "" ||
    //   country == "" ||
    //   state == "" ||
    //   zip == ""
    // ) {
    //   toast.warning("Enter Required Fields", {
    //     position: "bottom-right",
    //     autoClose: 1000,
    //     hideProgressBar: false,
    //     closeOnClick: true,
    //     pauseOnHover: true,
    //     draggable: true,
    //     progress: undefined,
    //   });
    // } else {
    let data = {
      user_id: userData._id,
      seller_id: loop[0].seller_id,
      subtotal: sum,
      total: sum,
      firstname: fname,
      lastname: lname,
      address1: address,
      address2: address2,
      country: country,
      state: state,
      zip: zip,
      paymenttype: payment,
      tokenid: transaction_id,
    };
    console.log("object", data);

    let result = await HttpClient.requestData("servicecheckout", "POST", data);
    console.log("result", result);
    if (result && result.status) {
      toast.success("Service booked successfully!", {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
      // reactLocalStorage.setObject("coupondetails", "");
      // reactLocalStorage.setObject("discount", "");
      setTimeout(() => {
        window.location.href = "/home";
      }, 3000);
    } else {
      toast.error("Service not booked", {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
    // }
  };

  const deletecart = async (item) => {
    let data = {};
    let result = await HttpClient.requestData(
      "servicecart/" + item,
      "DELETE",
      data
    );
    console.log("result", result);
    if (result && result.status) {
      getcart();
      window.location.href = "/home";
    } else {
    }
  };
  return (
    <div>
      <div className="container my-5">
        <div className="row">
          <ToastContainer />
          <div className="col-md-4 order-md-2 mb-4">
            {loading ? (
              <Loader />
            ) : (
              <>
                <div className="d-flex justify-content-between align-items-center mb-3">
                  <h4 className="text-muted mb-0">Your cart</h4>
                  <h5 className="mb-0">
                    <i className="fas fa-shopping-cart mr-2"></i>

                    <span
                      className="badge badge-danger badge-pill position-absolute"
                      style={{ fontSize: "13px", top: "0", right: "0" }}
                    >
                      {count}
                    </span>
                  </h5>
                </div>
                <ul className="list-group mb-3">
                  {loop.map((item, index) => {
                    return (
                      <div className="list-group-item ">
                        <li className="d-flex justify-content-between lh-condensed">
                          <div>
                            <h6 className="my-0">{item.servicename}</h6>

                            <small className="text-muted">
                              Brief description
                            </small>
                          </div>
                          <div className="d-flex">
                            <p className="text-muted">{item.price}</p>

                            {/* <span className="text-muted">
                            <i className="fas fa-times mx-2"></i> {item.qty}
                          </span> */}
                          </div>
                        </li>
                        <div className="text-right ">
                          <button
                            type="button"
                            className="btn"
                            onClick={() => deletecart(item._id)}
                          >
                            <i className="fas fa-trash-alt"></i>
                          </button>
                        </div>
                      </div>
                    );
                  })}

                  {/* <li className="list-group-item d-flex justify-content-between bg-light">
                    <div className="text-success">
                      <h6 className="my-0">Promo code</h6>
                      <small>{coupondetails.name}</small>
                    </div>
                    <span className="text-success">
                      - {Object.keys(discount).length == 0 ? 0 : discount}
                    </span>
                  </li> */}
                  <li className="list-group-item d-flex justify-content-between">
                    <span>Total (INR)</span>
                    <strong>{sum}</strong>
                  </li>
                </ul>
              </>
            )}
            {/* <form className="card p-2">
              <div className="input-group">
                <input
                  type="text"
                  className="form-control"
                  placeholder="Promo code"
                />
                <div className="input-group-append">
                  <button type="submit" className="btn checkout_btn">
                    Redeem
                  </button>
                </div>
              </div>
            </form> */}
          </div>
          <div className="col-md-8 order-md-1">
            <h4 className="mb-3">Billing address</h4>
            {/* <form> */}
            <div className="row">
              <div className="col-md-6 mb-3">
                <label htmlFor="firstName">
                  First name{" "}
                  <span class="required" style={{ color: "red" }}>
                    *
                  </span>
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="firstName"
                  placeholder="First Name"
                  value={fname}
                  onChange={(val) => {
                    if (val.target.value.match("^[a-zA-Z ]*$") != null) {
                      setfname(val.target.value);
                      if (val.target.value == "") {
                        setblank(false);
                      } else {
                        setblank(true);
                      }
                    }
                  }}
                />
              </div>
              <div className="col-md-6 mb-3">
                <label htmlFor="lastName">
                  Last name{" "}
                  <span class="required" style={{ color: "red" }}>
                    *
                  </span>
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="lastName"
                  placeholder="Last Name"
                  value={lname}
                  onChange={(val) => {
                    if (val.target.value.match("^[a-zA-Z ]*$") != null) {
                      setlname(val.target.value);
                      if (val.target.value == "") {
                        setblanklast(false);
                      } else {
                        setblanklast(true);
                      }
                    }
                  }}
                />
                <div className="invalid-feedback">
                  Valid last name is required.
                </div>
              </div>
            </div>

            <div className="mb-3">
              <label htmlFor="email">
                Email{" "}
                <span class="required" style={{ color: "red" }}>
                  *
                </span>
              </label>
              <input
                type="email"
                className="form-control"
                id="email"
                placeholder="Enter your email"
                value={email}
                onChange={(val) => {
                  setemail(val.target.value);
                  if (val.target.value == "") {
                    setblankemail(false);
                  } else {
                    setblankemail(true);
                  }
                }}
              />
              <div className="invalid-feedback">
                Please enter a valid email address for shipping updates.
              </div>
            </div>
            <div className="mb-3">
              <label htmlFor="address">
                Address{" "}
                <span class="required" style={{ color: "red" }}>
                  *
                </span>
              </label>
              <input
                type="text"
                className="form-control"
                id="address"
                placeholder="Enter your address"
                value={address}
                // onChange={(val) => setaddress(val.target.value)}
                onChange={(val) => {
                  setaddress(val.target.value);
                  if (val.target.value == "") {
                    setblankaddress(false);
                  } else {
                    setblankaddress(true);
                  }
                }}
              />
              <div className="invalid-feedback">
                Please enter your shipping address.
              </div>
            </div>
            <div className="mb-3">
              <label htmlFor="address2">
                Address 2 <span className="text-muted">(Optional)</span>
              </label>
              <input
                type="text"
                className="form-control"
                id="address2"
                placeholder="Enter your address2"
                value={address2}
                onChange={(val) => setaddress2(val.target.value)}
              />
            </div>
            <div className="row">
              <div className="col-md-5 mb-3">
                <label htmlFor="country">
                  Country{" "}
                  <span class="required" style={{ color: "red" }}>
                    *
                  </span>
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="country"
                  placeholder
                  value={country}
                  onChange={(val) => {
                    if (val.target.value.match("^[a-zA-Z ]*$") != null) {
                      setcountry(val.target.value);
                      if (val.target.value == "") {
                        setblankcountry(false);
                      } else {
                        setblankcountry(true);
                      }
                    }
                  }}
                />

                <div className="invalid-feedback">
                  Please select a valid country.
                </div>
              </div>
              <div className="col-md-4 mb-3">
                <label htmlFor="state">
                  State{" "}
                  <span class="required" style={{ color: "red" }}>
                    *
                  </span>
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="state"
                  placeholder
                  value={state}
                  onChange={(val) => {
                    if (val.target.value.match("^[a-zA-Z ]*$") != null) {
                      setstate(val.target.value);
                      if (val.target.value == "") {
                        setblankstate(false);
                      } else {
                        setblankstate(true);
                      }
                    }
                  }}
                />
                {/* <select
                    className="custom-select d-block w-100"
                    id="state"
                    required
                  >
                    <option value>Choose...</option>
                    <option>California</option>
                  </select> */}
                <div className="invalid-feedback">
                  Please provide a valid state.
                </div>
              </div>
              <div className="col-md-3 mb-3">
                <label htmlFor="zip">
                  Zip{" "}
                  <span class="required" style={{ color: "red" }}>
                    *
                  </span>
                </label>
                <input
                  type="text"
                  className="form-control"
                  id="zip"
                  placeholder
                  value={zip}
                  onChange={(val) => {
                    if (val.target.value.match("^[0-9]*$") != null) {
                      setzip(val.target.value);
                      if (val.target.value == "") {
                        setblankzip(false);
                      } else {
                        setblankzip(true);
                      }
                    }
                  }}
                />
                <div className="invalid-feedback">Zip code required.</div>
              </div>
            </div>
            <hr className="mb-4" />
            <div className="custom-control custom-checkbox">
              <input
                type="checkbox"
                className="custom-control-input"
                id="same-address"
              />
              <label className="custom-control-label" htmlFor="same-address">
                Shipping address is the same as my billing address
              </label>
            </div>
            <div className="custom-control custom-checkbox">
              <input
                type="checkbox"
                className="custom-control-input"
                id="save-info"
              />
              <label className="custom-control-label" htmlFor="save-info">
                Save this information for next time
              </label>
            </div>

            {blank &&
              blanklast &&
              blankemail &&
              blankaddress &&
              blankcountry &&
              blankstate &&
              blankzip && (
                <>
                  {" "}
                  <h4 className="mb-3">
                    Payment{" "}
                    <span class="required" style={{ color: "red" }}>
                      *
                    </span>
                  </h4>
                  <div className="d-block my-3">
                    <div className="custom-control custom-radio">
                      <input
                        id="stripe"
                        name="paymentMethod"
                        type="radio"
                        className="custom-control-input"
                        value="stripe"
                        checked={payment == "stripe" ? true : false}
                        onChange={handlepayment}
                      />
                      <label className="custom-control-label" htmlFor="stripe">
                        Stripe
                      </label>
                    </div>

                    <div className="custom-control custom-radio">
                      <input
                        id="paypal"
                        name="paymentMethod"
                        type="radio"
                        className="custom-control-input"
                        value="paypal"
                        checked={payment == "paypal" ? true : false}
                        onChange={handlepayment}
                      />
                      <label className="custom-control-label" htmlFor="paypal">
                        Paypal
                      </label>
                    </div>
                  </div>
                </>
              )}

            <hr className="mb-4" />
            {/* <button
                className="btn checkout_btn btn-lg btn-block"
                // type="submit"
                onClick={order}
              >
                Continue to checkout
              </button> */}
            {/* </form> */}
            {payment == "stripe" && (
              <StripeCheckout
                token={onToken}
                stripeKey="pk_test_51JavJLSB5KAxkRypwpyFZVGF7Mxwel9iBFVqCbIaqDQERfj9A6NoeJEaBBZnnGRgSCasukWcLtWICm7SI0CtSDAl00bKUxkGVB"
                amount={sum * 100}
              />
            )}
            {payment == "paypal" && (
              <PayPal total={sum} senddetails={senddetails} />
            )}
          </div>
        </div>
      </div>
      <section>
        <Footer />
      </section>
    </div>
  );
};
export default CheckoutService;
