import React, { useState } from "react";
import { useEffect } from "react";
import { Link } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import HttpClient from "../../utils/HttpClient";
import { reactLocalStorage } from "reactjs-localstorage";
const fileObj = [];
const fileArray = [];
const Index = () => {
  const [name, setname] = useState("");
  const [price, setprice] = useState("");
  const [details, setdetails] = useState("");
  const [personalisation, setpersonalisation] = useState("");
  const [hashtag, sethashtag] = useState("");
  const [serviceimg, setserviceimg] = useState("");

  const [arr, setarr] = useState([]);

  const [category, setcategory] = useState("");
  const [subcategory, setSubcategory] = useState("");

  const [cat, setcat] = useState([]);
  const [subcat, setsubcat] = useState([]);
  const [shopid, setshopid] = useState("");
  const [preview, setpreview] = useState([]);
  const [multiImages, setmultiImages] = useState([]);
  const [refresh, setrefresh] = useState(false);
  const [tax, settax] = useState(0);
  const [currency, setcurrency] = useState("");
  const [currencyTyp, setcurrencyTyp] = useState(
    [
      {
        name: "INR"
      },
      {
        name: "USD"
      },
      {
        name: "GBP"
      },
      {
        name: "EUR"
      },
      {
        name: "CAD"
      },
      {
        name: "cdd"
      }

    ],
  );

  const onImageId = async (e) => {
    if (e.target.files.length > 0) {
      const imageArr = Array.from(e.target.files);
      console.log("imageArr", imageArr);
      const imageUrlArray = [];
      imageArr.forEach((file) => {
        preview.push({ preview: URL.createObjectURL(file) });
      });
      setpreview(preview);
      imageUploadApi(imageArr);
      setrefresh(!refresh);
      // setTimeout(() => {
      //   console.log("previeww", preview);
      // }, 1000);
    }
  };

  const setImagesUrl = (url) => {
    let tempUrlArr = multiImages;
    tempUrlArr.push(url);
    setmultiImages(tempUrlArr);
    console.log("multiImages", multiImages);
  };

  const imageUploadApi = async (files) => {
    //need to start loader
    //  setshowLoder(true);
    console.log("files", files);
    for (const file of files) {
      let data = new FormData();
      data.append("image", file);
      let result = await HttpClient.fileUplode(
        "shop-service-images",
        "POST",
        data
      );
      console.log("reuslkyet", result);

      if (result && result.status) {
        setImagesUrl(result.data);
      }
    }
    //need to stop loader
    //  setshowLoder(false);
  };

  useEffect(() => {
    fetchCat();
    fetchShopId();
  }, []);

  const handlecategory = (event) => {
    const selectedcategory = event.target.value;
    setcategory(selectedcategory);
    fetchSubCat(selectedcategory);
  };

  const handleSubcategory = (event) => {
    const selectedSubcategory = event.target.value;
    setSubcategory(selectedSubcategory);
  };
  const fetchCat = async () => {
    let result = await HttpClient.requestData("service", "GET");
    // console.log("Cat", result);
    if (result && result.status) {
      let data = result.data;
      // console.log("data", data);
      setcat(data);
      // console.log("setcat", cat);
    } else {
    }
  };

  const fetchShopId = async () => {
    let userdata = reactLocalStorage.getObject("userData");
    // console.log("userdata", userdata);
    let result = await HttpClient.requestData("seller/" + userdata._id, "GET");
    console.log("Shop", result);
    if (result && result.status) {
      setshopid(result.shop_id ? result.shop_id : "");
      // console.log("shopid", shopid);
    } else {
    }
  };

  const deleteFile = (e) => {
    const s = preview.filter((item, index) => index !== e);
    setpreview(s);
    // console.log(s);
  };

  const fetchSubCat = async (id) => {
    let result = await HttpClient.requestData(
      "service/subcategory/" + id,
      "GET"
    );
    // console.log("SubCat", result);
    if (result && result.status) {
      let data = result.data;
      // console.log("subdata", data);
      setsubcat(data);
      // console.log("SubCatdata", subcat);
    } else {
    }
  };
  const publish = async () => {
    if (
      name == "" ||
      price == "" ||
      details == "" ||
      personalisation == "" ||
      hashtag == "" ||
      category.length == 0 ||
      subcategory.length == 0
    ) {
      toast.error("Please Enter All Fields", {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
      return;
    }
    if (preview.length == 0) {
      toast.error("Please Enter Images", {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
      return;
    }

    if (shopid == "") {
      toast.error("Please Create a shop first", {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
      return;
    }

    // console.log("object");
    let data = new FormData();
    data.append("name", name);
    data.append("price", price);
    data.append("details", details);
    data.append("category_id", category);
    data.append("subcategory_id", subcategory);
    data.append("personalization", personalisation);
    data.append("hashtags", hashtag);
    data.append("image", JSON.stringify(multiImages));
    data.append("shop_id", shopid);
    data.append("currency", currency);
    let result = await HttpClient.fileUplode("shop/services", "POST", data);
    console.log("result", result);
    if (result && result.status) {
      setname("");
      setprice("");
      setdetails("");
      setpersonalisation("");
      sethashtag("");
      setserviceimg("");
      setcategory("");
      setSubcategory("");
      setpreview([]);
      setmultiImages([]);
      toast.success("Services Added Successfully!", {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    } else {
      toast.error(result.message, {
        position: "bottom-right",
        autoClose: 1000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  };

  // const getTax = async (curr) => {
  //   let data = {
  //     currency: curr
  //   };
  //   console.log("data", data);
  //   let result = await HttpClient.requestData(
  //     "currency/tax-rates",
  //     "POST",
  //     data
  //   );
  //   console.log("result", result);
  //   if (result && result.status) {
  //     console.log("result", result.data);

  //     settax(result.data.tax_rate);

  //   }
  // }

  return (
    <div className="Add_service  ">
      <ToastContainer />
      <div className="row mt-3">
        <div className="col-md-12 text-center">
          <h1>Add Services</h1>
        </div>
        <div className="col-md-6 mx-auto">
          <div className="form-group my-4">
            <label htmlFor="">
              Service Name{" "}
              <span class="required" style={{ color: "red" }}>
                *
              </span>
            </label>
            <input
              type="email"
              className="form-control"
              id="exampleInputEmail1"
              aria-describedby="emailHelp"
              placeholder="Service Name"
              onChange={(val) => {
                if (val.target.value.match("^[a-zA-Z ]*$") != null) {
                  setname(val.target.value);
                  // if (val.target.value == "") {
                  //   setnameerror(true);
                  // } else {
                  //   setnameerror(false);
                  // }
                }
              }}
              value={name}
            />
          </div>


          <div className="form-group my-4">
            <label htmlFor="">
              Currency{" "}
              <span class="required" style={{ color: "red" }}>
                *
              </span>
            </label>
            <select
              className="form-control"
              onChange={(val) => {
                setcurrency(val.target.value);

                // getTax(val.target.value);
              }}
              value={currency}
            >
              <option value="">Select Currency</option>
              {currencyTyp.map((item, index) => {
                return (<option value={item.name}>{item.name}</option>)
              })}

            </select>
          </div>
          {/* {currency != "" ?
            <>
              <div className="form-group my-4">
                <label htmlFor="">
                  Tax(%){" "}
                  <span class="required" style={{ color: "red" }}>
                    *
                  </span>
                </label>
                <input
                  className="form-control"
                  type="number"
                  value={tax}
                  placeholder="Tax"
                  id="example-text-input"
                  onChange={(val) => {
                    settax(val.target.value);
                  }}
                />
              </div> */}

          <div className="form-group my-4">
            <label htmlFor="">
              Service Price{" "}
              <span class="required" style={{ color: "red" }}>
                *
              </span>
            </label>
            <input
              // type="number"
              inputmode="numeric"
              className="form-control"
              id="exampleInputEmail1"
              aria-describedby="emailHelp"
              placeholder="Service Price"
              onChange={(val) => {
                if (val.target.value.match("^[0-9]*$") != null) {
                  setprice(val.target.value);
                }
              }}
              value={price}
            />
          </div>
          {/* </>
            : null} */}



          <div className="form-group my-4">
            <label htmlFor="">
              Service Details{" "}
              <span class="required" style={{ color: "red" }}>
                *
              </span>
            </label>
            <textarea
              className="form-control"
              id="exampleFormControlTextarea1"
              rows={3}
              placeholder="Please Add Your Service Details"
              onChange={(val) => {
                if (val.target.value.match("^[a-zA-Z ]*$") != null) {
                  setdetails(val.target.value);
                }
              }}
              value={details}
            />
          </div>

          <div className="form-group my-4">
            <label htmlFor="">
              Add Your Personalization{" "}
              <span class="required" style={{ color: "red" }}>
                *
              </span>
            </label>
            <textarea
              className="form-control"
              id="exampleFormControlTextarea1"
              rows={3}
              placeholder=" Please Add Your Personalization"
              onChange={(val) => {
                if (val.target.value.match("^[a-zA-Z ]*$") != null) {
                  setpersonalisation(val.target.value);
                }
              }}
              value={personalisation}
            />
          </div>
          <div className="row">
            <div className="col-md-6  ">
              <div className="form-group">
                <select
                  className="form-control"
                  id="exampleFormControlSelect1"
                  onChange={handlecategory}
                  value={category}
                >
                  <option value="">Select Catagory</option>
                  {cat.map((item, index) => {
                    return (
                      <option value={item._id} key={index}>
                        {item.name}
                      </option>
                    );
                  })}
                </select>
              </div>
            </div>
            <div className="col-md-6">
              <div className="form-group">
                <select
                  className="form-control"
                  id="exampleFormControlSelect1"
                  onChange={handleSubcategory}
                  value={subcategory}
                >
                  <option>Select Sub Catagory</option>
                  {subcat.map((item, index) => {
                    return (
                      <option value={item._id} key={index}>
                        {item.name}
                      </option>
                    );
                  })}
                </select>
              </div>
            </div>
          </div>

          <div className="form-group my-4">
            <label htmlFor="">
              Hashtag for Services{" "}
              <span class="required" style={{ color: "red" }}>
                *
              </span>
            </label>
            <textarea
              className="form-control"
              id="exampleFormControlTextarea1"
              rows={3}
              placeholder="Please Add Hashtag for Service"
              onChange={(val) => {
                if (val.target.value.match("^[a-zA-Z ]*$") != null) {
                  sethashtag(val.target.value);
                }
              }}
              value={hashtag}
            />
          </div>
          <div className="d-flex justify-content-between">
            <div>
              <h5>
                Service Image{" "}
                <span class="required" style={{ color: "red" }}>
                  *
                </span>
              </h5>

              {preview.map((item, index) => (
                <div className="product-thumb">
                  <img
                    className="avatar ml-3 img-fluid img-thumbnail "
                    key={index}
                    style={{ width: "54px", height: "54px" }}
                    src={item.preview}
                    alt="..."
                  />
                  <button
                    type="button"
                    className="btn cross-btn"
                    onClick={() => deleteFile(index)}
                  >
                    <i className="far fa-times-circle"></i>
                  </button>
                </div>
              ))}
            </div>
            <div className="choose_file my-4">
              <input
                type="file"
                multiple
                className="input_file"
                onChange={onImageId}
              />
              Choose File
            </div>
          </div>

          <div className="d-flex justify-content-between my-5">
            <Link
              to="/seller-service"
              type="button"
              className="btn preview_btn"
            >
              Preview
            </Link>
            <button type="button" className="btn publish_btn" onClick={publish}>
              Publish
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};
export default Index;
