import React, { useEffect, useState } from "react";
import Footer from "../../Defaults/Footer";
import HttpClient from "../../utils/HttpClient";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { reactLocalStorage } from "reactjs-localstorage";

const Feedback = () => {
  const [feedback, setfeedback] = useState("");
  const userData = reactLocalStorage.getObject("userData");

  const feedbackSubmit = async () => {
    if (feedback == "") {
      toast.error("Please enter feedback", {
        position: "bottom-right",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    } else {
      let sendData = {
        name: userData.firstName + " " + userData.lastName,
        email: userData.email,
        feedback_detail: feedback,
      };
      let result = await HttpClient.requestData(
        "feedback",
        "POST",
        sendData
      );
      // console.log("submit feedback", result);
      if (result && result.status) {
        toast.success("Feedback Added Successfully", {
          position: "bottom-right",
          autoClose: 1000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
        setfeedback("");
      } else {
        toast.error("Error in Adding Feedback", {
          position: "bottom-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
      }
    }
  };

  return (
    <div className="feedback">
      <ToastContainer />
      <div className="container-fluid">
        <div className="row">
          <div className="col-md-12 text-center my-4">
            <h2>Feedback</h2>
          </div>
          <div className="col-md-6 m-auto">
            <div className="form-group">
              <label htmlFor="exampleFormControlTextarea1">
                Give Your Feedback Here
              </label>
              <textarea
                className="form-control"
                id="exampleFormControlTextarea1"
                rows={5}
                placeholder="Write Here..."
                value={feedback}
                onChange={(val) => {
                  if (val.target.value.match("^[a-zA-Z0-9 ]*$") != null) {
                    setfeedback(val.target.value);
                  }
                }}
              />
            </div>
            <div className="text-center my-4">
              <button className="btn submit_btn" onClick={feedbackSubmit}>
                Submit
              </button>
            </div>
          </div>
        </div>
      </div>
      <section className="mt-5">
        <Footer />
      </section>
    </div>
  );
};

export default Feedback;
